.main {
  overflow            : hidden;
  position            : relative;
  width               : 100%;
  height              : 100%;
  // background-image : url('../../assets/image/back/home_back.png');
  // background-repeat: no-repeat;
  // background-size  : 100% 100%;

  .home {
    width           : 100%;
    height          : 100%;
    overflow        : hidden;
    position        : relative;
    background-color: transparent;

    .button {
      pointer-events: all;

      &:hover {
        cursor: pointer;
      }
    }

    .world {
      width   : 100%;
      height  : 100%;
      position: relative;

      .notification_container {
        width   : 430px;
        height  : 362px;
        position: absolute;
        top     : 135px;
        left    : -380px;

        .rel_container {
          width   : 100%;
          height  : 100%;
          position: relative;
        }
      }

      .left_border_img_container {
        position: absolute;
        left    : -100px;
        top     : 0;
        height  : 100%;

        .left_border_img {
          transform: translate(38px, 0);
          height   : 100%;
          width    : 100px;
        }
      }

      .world_logo_control_container {
        position: absolute;
        left    : 47px;
        top     : 20px;
        width   : 53px;

        .world_logo_container {
          width           : 100%;
          height          : 53px;
          border-radius   : 50%;
          border          : none;
          background-color: #F5C242;
          pointer-events  : all;

          .left_border_img {
            width        : 53px;
            height       : 53px;
            border-radius: 50%;
            border       : none;
          }

          &:hover {
            cursor: pointer;
          }
        }

        .world_logo_text {
          margin-top    : 8px;
          border        : none;
          border-radius : 18px;
          background    : #E53222;
          width         : 104px;
          height        : 34px;
          display       : flex;
          align-items   : center;
          padding-left  : 32px;
          transform     : translate(-50%, 0);
          color         : #FFF;
          pointer-events: all;
        }
      }

      .world_header_btn_container {
        display        : flex;
        justify-content: center;
        position       : absolute;
        left           : 0;
        top            : 0;
        width          : 100%;
        height         : 100%;
        margin-top     : 20px;

        .button {
          display        : flex;
          justify-content: center;

          .world_btn {
            height: 100%;
          }
        }

        .notification_btn {
          position: relative;

          .click_btn {
            width          : 100%;
            height         : 100%;
            border-radius  : 30px;
            border         : none;
            display        : flex;
            justify-content: center;
          }
        }

        .world_header_small_btn {
          width        : 82px;
          height       : 61px;
          border-radius: 30px;
          background   : #E53222;
          margin-left  : 37.5px;
          margin-right : 37.5px;
          box-shadow   : 0px 4px 20px 0px #00000040;

          .world_btn {
            height: 61px;
          }
        }

        .world_header_medium_btn {
          width        : 96px;
          height       : 72px;
          border-radius: 30px;
          background   : #E53222;
          margin-left  : 37.5px;
          margin-right : 37.5px;
          box-shadow   : 0px 4px 20px 0px #00000040;

          .world_btn {
            height: 72px;
          }
        }

        .world_header_large_btn {
          width        : 131px;
          height       : 98px;
          border-radius: 30px;
          background   : #E53222;
          margin-left  : 37.5px;
          margin-right : 37.5px;
          box-shadow   : 0px 4px 20px 0px #00000040;

          .world_btn {
            height: 98px;
          }
        }
      }

      .world_footer_btn_container {
        display        : flex;
        justify-content: flex-start;
        position       : absolute;
        left           : 36px;
        bottom         : 21px;
        width          : 100%;


        .world_header_medium_btn {
          width        : 96px;
          height       : 72px;
          border-radius: 30px;
          background   : #E53222;
          margin-right : 11px;
          box-shadow   : 0px 4px 20px 0px #00000040;
        }

      }

    }

  }
}

