.chatmain {
    width         : 308px;
    height        : 318px;
    border        : none;
    border-radius : 11px;
    background    : #E53222;
    box-shadow    : 6px 4px 5px 0px #0000001A;
    pointer-events: all;
    margin-right  : 38px;
    z-index       : 10;

    .chatmain_container {
        width : 100%;
        height: 100%;

        .chatmain_header {
            width        : 100%;
            height       : 62px;
            padding      : 13px 7.5px 0 10px;
            border-bottom: 1px solid #FFF;

            .chatmain_header_container {
                position: relative;
                width   : 100%;
                height  : 100%;

                .user_tab {
                    position      : absolute;
                    left          : 0;
                    bottom        : 0;
                    pointer-events: all;

                    .user_tab_container {
                        width                  : 156px;
                        height                 : 33px;
                        background             : #B02418;
                        border                 : none;
                        border-top-left-radius : 8px;
                        border-top-right-radius: 8px;
                        padding                : 5px 7px 4px 12px;

                        .user_tab_content {
                            width      : 100%;
                            height     : 100%;
                            position   : relative;
                            display    : flex;
                            align-items: center;

                            .user_tab_avatar {
                                width           : 24px;
                                height          : 24px;
                                border          : none;
                                border-radius   : 50%;
                                background-color: #C4C4C4;
                            }

                            .user_tab_name {
                                font-family   : Poppins;
                                font-size     : 10px;
                                font-style    : normal;
                                font-weight   : 500;
                                line-height   : 15px;
                                letter-spacing: 0em;
                                color         : #FFF;
                                margin-left   : 10px;
                            }

                            .user_tab_close_btn_container {
                                position      : absolute;
                                right         : 0;
                                top           : 0;
                                width         : 10px;
                                height        : 10px;
                                display       : flex;
                                pointer-events: all;
                                color         : #FFF;
                            }

                            .user_tab_close_btn_container:hover {
                                cursor: pointer;
                            }
                        }
                    }
                }

            }

        }

        .chatmain_header:hover {
            cursor: grab;
        }


        .chatmain_header:active {
            cursor: grabbing;
        }
    }

    .chat_content {
        width  : 100%;
        height : 256px;
        padding: 10px 6px 10px 10px;

        .content {
            position: relative;
            width   : 100%;
            height  : 100%;

            .chat_content_container {
                width         : 100%;
                height        : 190px;
                padding       : 0 10px 10px 9px;
                overflow-x    : hidden;
                overflow-y    : auto;
                display       : flex;
                flex-direction: column-reverse;

                &::-webkit-scrollbar {
                    width: 4px;
                }

                &::-webkit-scrollbar-track {
                    background-color: transparent;
                    border-radius   : 100px;
                }

                &::-webkit-scrollbar-thumb {
                    border-radius   : 100px;
                    background-clip : content-box;
                    background-color: rgba(255, 255, 255, 0.1);
                }
            }



            .chat_input_container {
                position     : absolute;
                left         : 0;
                bottom       : 0;
                width        : 100%;
                height       : 46px;
                border       : none;
                border-radius: 12px;
                background   : #B02418;
                padding      : 0 15px 0 20px;

                .chat_input {
                    width      : 100%;
                    height     : 100%;
                    display    : flex;
                    align-items: center;

                    .input {
                        width           : 100%;
                        height          : 20px;
                        background-color: transparent;
                        border          : none;
                        font-family     : Poppins;
                        font-size       : 10px;
                        font-style      : normal;
                        font-weight     : 500;
                        line-height     : 15px;
                        letter-spacing  : 0em;
                        text-align      : left;
                        outline         : none;
                        color           : #FFF;
                    }

                    ::placeholder {
                        color: #FFF;
                    }

                    .chat_three_dot {
                        width                : 11px;
                        color                : #FFF;
                        margin-left          : 8px;
                        margin-right         : 20px;
                        -webkit-text-security: disc;
                        font-size            : 13px;
                    }

                    .chat_three_dot:hover {
                        cursor: pointer;
                    }

                    .chat_send_btn {
                        width : 19px;
                        height: 16px;
                    }

                    .chat_send_btn:hover {
                        cursor: pointer;
                    }
                }
            }
        }
    }
}

