.forum {
    width           : 100%;
    padding         : 12px 25px 10px 10px;
    box-shadow      : 0px 3.573619842529297px 16.438650131225586px -8.57668685913086px #00000040;
    border          : 0.71px solid #EBEBEB;
    border-radius   : 14px;
    background-color: #ffffff;
    margin-top      : 15px;
    position        : relative;

    .forum_container {
        display: flex;

        .forum_avatar {
            width        : 47px;
            height       : 47px;
            border       : none;
            border-radius: 50%;
            margin-right : 10px;
        }

        .forum_avatar.avatar_active {
            width        : 47px;
            height       : 47px;
            border       : 1.15px solid #F5C242;
            border-radius: 50%;
            margin-right : 10px;
        }

        .forum_content {
            width: 100%;

            .content_name {
                width         : 100%;
                font-family   : Poppins;
                font-size     : 10px;
                font-style    : normal;
                font-weight   : 700;
                line-height   : 12px;
                letter-spacing: 0em;
                text-align    : left;
                color         : var(--red);
            }

            .content {
                width         : 100%;
                margin-top    : 6px;
                font-family   : Poppins;
                font-size     : 14px;
                font-style    : normal;
                font-weight   : 400;
                line-height   : 21px;
                letter-spacing: 0.02em;
                text-align    : left;
                color         : #535353;
            }

            .control_btn_container {
                width     : 100%;
                margin-top: 6px;
                display   : flex;

                .control_btn {
                    width           : 24px;
                    height          : 24px;
                    border          : 0.71px solid #E53222;
                    border-radius   : 50%;
                    background-color: #FFF;
                    display         : flex;
                    align-items     : center;
                    justify-content : center;

                    * {
                        color: #E53222;
                    }
                }

                .control_btn:hover {
                    cursor: pointer;
                }

                .control_btn.active {
                    background-color: #E53222;

                    * {
                        color: #FFF;
                    }
                }

                .mar_r_8 {
                    margin-right: 8px;
                }
            }
        }

        .active {
            width : 9px;
            height: 9px;
        }
    }
}

.forum.lastview {
    &::after {
        position         : absolute;
        content          : "";
        left             : 0px;
        top              : 0px;
        height           : 100%;
        width            : 100%;
        background       : linear-gradient(transparent, white);
        pointer-events   : none;
        // border-radius : 14px;
    }
}