.video_text {
    width  : 100%;
    padding: 11px 0 18px 0;

    .text_item {
        width          : 100%;
        padding        : 0 42px 16px 206px;
        display        : flex;
        justify-content: space-between;

        .title,
        .info {
            font-family   : Poppins;
            font-size     : 14px;
            font-style    : normal;
            font-weight   : 400;
            line-height   : 21px;
            letter-spacing: 0.02em;
            text-align    : left;
            color         : var(--grey);
        }


    }

    .text_item:last-child {
        padding-bottom: 0;
    }
}