.university_content {
  width                        : 100%;
  border                       : none;
  border-radius                : 30px;
  border-bottom-left-radius    : 0;
  border-bottom-right-radius   : 0;
  background-color             : #FFF;
  position                     : relative;
  // height                    : 200px;

  .tab_btn_container {
    position : absolute;
    top      : 0;
    left     : 50%;
    transform: translate(-50%, -100%);

    .tab_btn {
      display        : flex;
      justify-content: center;

      .tab_first_btn {
        height                : 27px;
        width                 : 187px;
        border-top-left-radius: 50px;
        border                : none;
      }


      .tab_second_btn {
        height                 : 27px;
        width                  : 187px;
        border-top-right-radius: 50px;
        border                 : none;
      }

      .tab_first_btn:hover,
      .tab_second_btn:hover {
        cursor: pointer;
      }

      .w_back {
        background-color: #FFF;
        color           : #E53222;
      }

      .r_back {
        background-color: #EE8885;
        color           : #FFF;
      }
    }
  }

  .instructor_info {
    width         : 100%;
    padding-top   : 30px;
    padding-right : 30px;
    padding-left  : 30px;
    padding-bottom: 38px;

    .header {
      width: 100%;

      .title {
        font-family   : Poppins;
        font-size     : 24px;
        font-style    : normal;
        font-weight   : 700;
        line-height   : 36px;
        letter-spacing: 0em;
        text-align    : left;
        color         : var(--red);
      }

      .subtitle {
        margin-top    : 2px;
        font-family   : Poppins;
        font-size     : 18px;
        font-style    : normal;
        font-weight   : 400;
        line-height   : 27px;
        letter-spacing: 0em;
        text-align    : left;
        color         : var(--grey);

        &>a {
          color: var(--grey);
        }
      }
    }

    .info_container {
      width          : 100%;
      margin-top     : 48px;
      display        : flex;
      justify-content: flex-start;

      .left_info {
        width: 210px;

        .avatar {
          width : 210px;
          height: 210px;

          .active_icon {
            width : 40px;
            height: 40px;
          }

          .active {
            width        : 210px;
            height       : 210px;
            border       : 5px solid #F5C242;
            border-radius: 50%;
          }

          .avatarImg {
            width        : 210px;
            height       : 210px;
            border-radius: 50%;

            &>img {
              width        : 100%;
              height       : 100%;
              border-radius: 50%;
            }
          }
        }

        .online_status {
          width     : 100%;
          margin-top: 23px;

          .online {
            font-family        : Poppins;
            font-size          : 20px;
            font-style         : normal;
            font-weight        : 500;
            line-height        : 26px;
            letter-spacing     : 0.02em;
            text-align         : center;
            color              : #039A00;
            display            : list-item;
            list-style-position: inside;

            span {
              position: relative;
              color   : #039A00;
              left    : -12px;
            }
          }

          .offline {
            font-family        : Poppins;
            font-size          : 20px;
            font-style         : normal;
            font-weight        : 500;
            line-height        : 26px;
            letter-spacing     : 0.02em;
            text-align         : center;
            color              : #C4C4C4;
            display            : list-item;
            list-style-position: inside;

            span {
              position: relative;
              color   : #C4C4C4;
              left    : -12px;
            }
          }


        }

        .social_btn_container {
          width          : 100%;
          margin-top     : 36px;
          display        : flex;
          justify-content: space-between;

          .social_btn {
            width              : 32px;
            height             : 32px;
            border             : none;
            background         : transparent;
            // border-radius   : 10px;
            // color           : #FFF;
            // background-color: var(--red);
            display            : flex;
            align-items        : center;
            justify-content    : center;

            a {
              width : 100%;
              height: 100%;

              &>img {
                width : 100%;
                height: 100%;
              }
            }
          }
        }
      }

      .right_info {
        padding-left: 42px;
        width       : 100%;

        .header {
          width: 100%;

          .title {
            font-family   : Poppins;
            font-size     : 40px;
            font-style    : normal;
            font-weight   : 700;
            line-height   : 60px;
            letter-spacing: 0.02em;
            text-align    : left;
            color         : var(--red);
          }

          .subtitle {
            font-family   : Poppins;
            font-size     : 20px;
            font-style    : normal;
            font-weight   : 500;
            line-height   : 26px;
            letter-spacing: 0.02em;
            text-align    : left;
            color         : var(--grey);
          }
        }

        .more_info {
          width          : 100%;
          display        : flex;
          justify-content: space-between;
          margin-top     : 24px;

          .students,
          .course {
            display: flex;

            .icon {
              width: 40px;

              &>img {
                width : 40px;
                height: 40px;
              }
            }

            .content {
              margin-left: 16px;

              .title {
                height        : 40px;
                font-family   : Poppins;
                font-size     : 20px;
                font-style    : normal;
                font-weight   : 500;
                line-height   : 26px;
                letter-spacing: 0.02em;
                text-align    : left;
                color         : var(--red);
                display       : flex;
                align-items   : center;
              }

              .count {
                font-family   : Poppins;
                font-size     : 20px;
                font-style    : normal;
                font-weight   : 500;
                line-height   : 26px;
                letter-spacing: 0.02em;
                text-align    : left;
                color         : #B4B4B4;
              }
            }
          }

          .chat_btn {
            width           : 209px;
            height          : 52px;
            border          : none;
            border-radius   : 10px;
            background-color: #F5C242;
            color           : #FFF;
            font-family     : Poppins;
            font-size       : 20px;
            font-style      : normal;
            font-weight     : 500;
            line-height     : 26px;
            letter-spacing  : 0.02em;
            text-align      : left;
            display         : flex;
            align-items     : center;

            .icon {
              width       : 32px;
              height      : 30px;
              margin-left : 20px;
              margin-right: 14px;

              &>img {
                width : 100%;
                height: 100%;
              }
            }

            &:hover {
              cursor: pointer;
            }
          }
        }

        .description {
          width     : 100%;
          margin-top: 24px;

          .title {
            font-family   : Poppins;
            font-size     : 20px;
            font-style    : normal;
            font-weight   : 500;
            line-height   : 26px;
            letter-spacing: 0.02em;
            text-align    : left;
            color         : var(--grey);
          }

          .content {
            width         : 100%;
            height        : 105px;
            margin-top    : 16px;
            font-family   : Poppins;
            font-size     : 14px;
            font-style    : normal;
            font-weight   : 400;
            line-height   : 21px;
            letter-spacing: 0.02em;
            text-align    : left;
            color         : var(--grey);
            position      : relative;
            overflow      : hidden;

            &::after {
              position         : absolute;
              content          : "";
              left             : 0px;
              top              : 0px;
              height           : 100%;
              width            : 100%;
              background       : linear-gradient(transparent, white);
              pointer-events   : none;
              // border-radius : 14px;
            }
          }

          .all_content {
            width         : 100%;
            margin-top    : 16px;
            font-family   : Poppins;
            font-size     : 14px;
            font-style    : normal;
            font-weight   : 400;
            line-height   : 21px;
            letter-spacing: 0.02em;
            text-align    : left;
            color         : var(--grey);
          }

          .more_view {
            width: 100%;

            &>button {
              width           : 100%;
              height          : 48px;
              border          : 1px solid #E53222;
              border-radius   : 10px;
              background-color: #FFF;
              font-family     : Poppins;
              font-size       : 14px;
              font-style      : normal;
              font-weight     : 400;
              line-height     : 21px;
              letter-spacing  : 0.02em;
              text-align      : center;
              color           : var(--red);
              transition : color 0.2s, background-color 0.2s;
              transition-timing-function: ease;
            }

            &>button:hover {
              cursor: pointer;
              background-color: var(--red);
              color           : #FFF;
            }
          }
        }
      }
    }

    .courses {
      width     : 100%;
      margin-top: 96px;

      .title {
        font-family   : Poppins;
        font-size     : 20px;
        font-style    : normal;
        font-weight   : 500;
        line-height   : 26px;
        letter-spacing: 0.02em;
        text-align    : left;
        color         : var(--grey);
      }

      .content {
        width     : 100%;
        margin-top: 14px;
      }
    }

    .forum {
      width     : 100%;
      margin-top: 96px;

      .forum_title {
        font-family   : Poppins;
        font-size     : 20px;
        font-style    : normal;
        font-weight   : 500;
        line-height   : 26px;
        letter-spacing: 0.02em;
        text-align    : left;
        color         : var(--grey);
      }

      .forum_container {
        width: 100%;

        .more_view_btn {
          width           : 100%;
          height          : 38px;
          background-color: #FFF;
          border          : 1px solid #E53222;
          border-radius   : 10px;
          font-family     : Poppins;
          font-size       : 14px;
          font-style      : normal;
          font-weight     : 400;
          line-height     : 21px;
          letter-spacing  : 0.02em;
          text-align      : center;
          color           : var(--red);
          transition      : color 0.5s,
            background-color 0.5s;
          transition-timing-function: ease;

          &:hover {
            cursor          : pointer;
            background-color: var(--red);
            color           : #FFF;
          }
        }
      }

      .related_course {
        width        : 100%;
        margin-top   : 96px;
        margin-bottom: 127px;

        .title {
          font-family   : Poppins;
          font-size     : 20px;
          font-style    : normal;
          font-weight   : 500;
          line-height   : 26px;
          letter-spacing: 0.02em;
          text-align    : left;
          color         : var(--grey);
        }

        .content {
          width: 100%;
        }
      }
    }


  }


  .university_second_content_container {
    background-color: #FFF;
    width           : 100%;
    height          : 1000px;
  }
}