.main {
  overflow            : hidden;
  position            : relative;
  width               : 100%;
  height              : 100%;
  // background-image : url('../../assets/image/back/home_back.png');
  // background-repeat: no-repeat;
  // background-size  : 100% 100%;
  pointer-events      : all;

  .edit_info {
    width           : 595px;
    height          : 76%;
    position        : absolute;
    left            : 50%;
    top             : 23%;
    transform       : translate(-50%, 0);
    background-color: transparent;
    overflow        : auto;

    .edit_card {
      width           : 100%;
      height          : 500px;
      position        : relative;
      background-color: #FFF;
      border          : none;
      border-radius   : 30px;
      padding         : 60px 35px 17px 35px;
      display         : flex;

      .left_section {
        width: 277px;

        .email {
          width   : 277px;
          position: relative;

          .title {
            font-family   : Poppins;
            font-size     : 18px;
            font-style    : normal;
            font-weight   : 600;
            line-height   : 41px;
            letter-spacing: 0em;
            text-align    : left;
            color         : #B02418;
          }

          .input {
            width: 100%;

            &>input {
              width           : 100%;
              height          : 36px;
              border          : 1px solid #B4B4B4;
              border-radius   : 10px;
              padding-left    : 8px;
              font-family     : Poppins;
              font-size       : 18px;
              font-style      : normal;
              font-weight     : 400;
              line-height     : 36px;
              letter-spacing  : 0em;
              text-align      : left;
              color           : var(--grey);
              background-color: #FFF;
              outline         : none;

              &:focus {
                color: #B4B4B4;
              }
            }
          }

          .error_input {
            width   : 100%;
            position: relative;

            &>input {
              width           : 100%;
              height          : 36px;
              border          : 1px solid #FF0000;
              border-radius   : 10px;
              padding-left    : 8px;
              font-family     : Poppins;
              font-size       : 18px;
              font-style      : normal;
              font-weight     : 400;
              line-height     : 36px;
              letter-spacing  : 0em;
              text-align      : left;
              color           : var(--grey);
              background-color: #FFF;
              outline         : none;

              &:focus {
                color: #B4B4B4;
              }
            }

            .error_icon {
              width           : 17px;
              height          : 17px;
              border-radius   : 5px;
              background-color: #FF0000;
              display         : flex;
              justify-content : center;
              align-items     : center;
              color           : #FFF;
              position        : absolute;
              top             : 50%;
              right           : 15px;
              transform       : translate(0, -50%);

              // transform: rotate(180deg);
              // font-size: 12px;

              .icon {
                user-select: none;
                transform  : rotate(180deg);
                font-size  : 12px;
              }
            }

            .error_message {
              width         : 100%;
              color         : #FF0000;
              font-family   : Poppins;
              font-size     : 10px;
              font-style    : normal;
              font-weight   : 400;
              line-height   : 25px;
              letter-spacing: 0em;
              text-align    : right;
              position      : absolute;
              bottom        : 0;
              right         : 0;
              transform     : translate(0, 100%);
            }
          }
        }

        .confirm_email {
          width   : 100%;
          position: relative;

          .title {
            font-family   : Poppins;
            font-size     : 10px;
            font-style    : normal;
            font-weight   : 400;
            line-height   : 25px;
            letter-spacing: 0em;
            text-align    : left;
            color         : var(--grey);
          }

          .input {
            width   : 100%;
            position: relative;

            &>input {
              width           : 100%;
              height          : 36px;
              border          : 1px solid #B4B4B4;
              border-radius   : 10px;
              padding-left    : 8px;
              font-family     : Poppins;
              font-size       : 18px;
              font-style      : normal;
              font-weight     : 400;
              line-height     : 36px;
              letter-spacing  : 0em;
              text-align      : left;
              color           : var(--grey);
              background-color: #FFF;
              outline         : none;

              &:focus {
                color: #B4B4B4;
              }
            }
          }

          .error_input {
            width   : 100%;
            position: relative;

            &>input {
              width           : 100%;
              height          : 36px;
              border          : 1px solid #FF0000;
              border-radius   : 10px;
              padding-left    : 8px;
              font-family     : Poppins;
              font-size       : 18px;
              font-style      : normal;
              font-weight     : 400;
              line-height     : 36px;
              letter-spacing  : 0em;
              text-align      : left;
              color           : #B4B4B4;
              background-color: #FFF;
              outline         : none;
            }

            .error_icon {
              width           : 17px;
              height          : 17px;
              border-radius   : 5px;
              background-color: #FF0000;
              display         : flex;
              justify-content : center;
              align-items     : center;
              color           : #FFF;
              position        : absolute;
              top             : 50%;
              right           : 15px;
              transform       : translate(0, -50%);

              // transform: rotate(180deg);
              // font-size: 12px;

              .icon {
                user-select: none;
                transform  : rotate(180deg);
                font-size  : 12px;
              }
            }

            .error_message {
              width         : 100%;
              color         : #FF0000;
              font-family   : Poppins;
              font-size     : 10px;
              font-style    : normal;
              font-weight   : 400;
              line-height   : 25px;
              letter-spacing: 0em;
              text-align    : right;
              position      : absolute;
              bottom        : 0;
              right         : 0;
              transform     : translate(0, 100%);
            }
          }
        }

        .office {
          width   : 100%;
          position: relative;

          .title {
            font-family   : Poppins;
            font-size     : 18px;
            font-style    : normal;
            font-weight   : 600;
            line-height   : 41px;
            letter-spacing: 0em;
            text-align    : left;
            color         : #B02418;
          }

          .input {
            width   : 100%;
            position: relative;

            &>select {
              width           : 100%;
              height          : 36px;
              border          : 1px solid #B4B4B4;
              border-radius   : 10px;
              padding-left    : 8px;
              padding-right   : 18px;
              font-family     : Poppins;
              font-size       : 18px;
              font-style      : normal;
              font-weight     : 400;
              line-height     : 36px;
              letter-spacing  : 0em;
              text-align      : left;
              color           : var(--grey);
              background-color: #FFF;
              outline         : none;
              appearance      : none;
            }

            .select_icon {
              position : absolute;
              top      : 50%;
              right    : 16px;
              transform: translate(0, -50%);
              width    : 20px;
              height   : 20px;

              .arrow_down {
                color    : #D3D3D3;
                font-size: 20px;
              }
            }
          }
        }

        .password {
          width   : 100%;
          position: relative;

          .title {
            font-family   : Poppins;
            font-size     : 18px;
            font-style    : normal;
            font-weight   : 600;
            line-height   : 41px;
            letter-spacing: 0em;
            text-align    : left;
            color         : #B02418;
          }

          .current {
            width: 100%;

            .subtitle {
              font-family   : Poppins;
              font-size     : 10px;
              font-style    : normal;
              font-weight   : 400;
              line-height   : 25px;
              letter-spacing: 0em;
              text-align    : left;
              color         : var(--grey);
            }

            .input {
              width   : 100%;
              position: relative;

              &>input {
                width                  : 100%;
                height                 : 36px;
                border                 : 1px solid #C4C4C4;
                border-radius          : 10px;
                padding-left           : 8px;
                font-family            : Poppins;
                font-size              : 18px;
                font-style             : normal;
                font-weight            : 400;
                line-height            : 36px;
                letter-spacing         : 0em;
                text-align             : left;
                color                  : var(--grey);
                background-color       : #FFF;
                outline                : none;
                //-webkit-text-security: disc;

                &:focus {
                  color: #AAAAAA;
                }
              }

              .input_icon {
                position : absolute;
                top      : 50%;
                right    : 12px;
                transform: translate(0, -50%);
                width    : 24px;
                height   : 24px;
                color    : #D3D3D3;

                &:hover {
                  cursor: pointer;
                }
              }
            }
          }

          .new {
            width: 100%;

            .subtitle {
              font-family   : Poppins;
              font-size     : 10px;
              font-style    : normal;
              font-weight   : 400;
              line-height   : 25px;
              letter-spacing: 0em;
              text-align    : left;
              color         : var(--grey);
            }

            .input {
              width   : 100%;
              position: relative;

              &>input {
                width                   : 100%;
                height                  : 36px;
                border                  : 1px solid #FFC000;
                border-radius           : 10px;
                padding-left            : 8px;
                font-family             : Poppins;
                font-size               : 18px;
                font-style              : normal;
                font-weight             : 400;
                line-height             : 36px;
                letter-spacing          : 0em;
                text-align              : left;
                color                   : #FFC000;
                background-color        : #FFF;
                outline                 : none;
                -webkit-text-security   : disc;
                // margin-right         : 40px;

              }

              .input_icon {
                position : absolute;
                top      : 50%;
                right    : 12px;
                transform: translate(0, -50%);
                width    : 24px;
                height   : 24px;
                color    : #FFC000;

                &:hover {
                  cursor: pointer;
                }
              }
            }
          }

        }

      }

      .right_section {
        padding-left: 62px;
        width       : 100%;

        .title {
          width         : 100%;
          font-family   : Poppins;
          font-size     : 18px;
          font-style    : normal;
          font-weight   : 600;
          line-height   : 41px;
          letter-spacing: 0em;
          text-align    : left;
          color         : #B02418;
        }

        .social_input {
          width        : 100%;
          margin-bottom: 21px;
          display      : flex;
          align-items  : center;

          .social_icon {
            width           : 32px;
            height          : 32px;
            border          : none;
            border-radius   : 10px;
            background-color: var(--red);
            color           : #FFF;
            display         : flex;
            justify-content : center;
            align-items     : center;
            font-size       : 22px;

            // &>* {
            //   transform: translate(0, 4px);
            // }
          }

          .input {
            padding-left: 4px;

            &>input {
              width         : 150px;
              height        : 36px;
              border        : 1px solid #C4C4C4;
              border-radius : 10px;
              padding       : 0 8px;
              font-family   : Poppins;
              font-size     : 16px;
              font-style    : normal;
              font-weight   : 400;
              line-height   : 36px;
              letter-spacing: 0em;
              text-align    : left;
              color         : var(--grey);
              outline       : none;

              &:focus {
                color: #AAAAAA;
              }
            }
          }
        }
      }

      .save {
        position : absolute;
        left     : 50%;
        bottom   : 22px;
        transform: translateX(-124px);


        .save_container {
          position: relative;
          display : flex;

          .prev_btn {
            width           : 19px;
            height          : 31px;
            border-radius   : 18px;
            border          : 1px solid #F5C242;
            background-color: #FFF;
            color           : #F5C242;
            display         : flex;
            justify-content : center;
            align-items     : center;
            margin-right    : 16px;

            &:hover {
              cursor: pointer;
            }
          }

          .save_btn {
            width           : 178px;
            height          : 31px;
            border          : none;
            border-radius   : 12px;
            background-color: #F5C242;
            display         : flex;
            justify-content : center;
            align-items     : center;
            font-family     : Poppins;
            font-size       : 14px;
            font-style      : normal;
            font-weight     : 600;
            line-height     : 21px;
            letter-spacing  : 0em;
            color           : #FFF;

            &:hover {
              cursor: pointer;
            }
          }
        }
      }

      .alert {
        width           : 234px;
        height          : 42px;
        border-radius   : 30px;
        box-shadow      : 0px 4px 4px 0px #00000040;
        display         : flex;
        justify-content : center;
        align-items     : center;
        font-family     : Poppins;
        font-size       : 11px;
        font-style      : normal;
        font-weight     : 500;
        line-height     : 48px;
        letter-spacing  : 0em;
        text-align      : center;
        background-color: #FFF;
        color           : #039A00;
        position        : absolute;
        left            : 50%;
        bottom          : 67px;
        transform       : translate(-50%, 0);

        &:hover {
          cursor: pointer;
        }
      }
    }

    &::-webkit-scrollbar {
      width: 8px;
    }

    &::-webkit-scrollbar-track {
      background-color: unset;
      border-radius   : 100px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: transparent;
      border          : 3px solid rgba(0, 0, 0, 0);
      background-clip : padding-box;
      border-radius   : 6px;
    }

    &:hover::-webkit-scrollbar-thumb {
      background-color: rgba(177, 34, 34, 1);
    }
  }

