  .avatar_container {
    width           : 141px;
    height          : 141px;
    border-radius   : 50%;
    border          : none;
    background-color: #F5C242;
    position        : absolute;
    top             : 23%;
    left            : 50%;
    transform       : translate(-50%, -50%);
    box-shadow      : 0px 4px 17px 0px #00000040;
    z-index         : 10;

    .rel_container {
      width   : 100%;
      height  : 100%;
      position: relative;

      &>img {
        width        : 100%;
        height       : 100%;
        border-radius: 50%;
        border       : none;
      }

      .edit_btn {
        position        : absolute;
        right           : 10px;
        bottom          : 2px;
        width           : 26px;
        height          : 26px;
        background-color: #FFF;
        color           : #F5C242;
        border          : none;
        border-radius   : 50%;
        box-shadow      : 0px 4px 4px 0px #00000040;

        &:hover {
          cursor: pointer;
        }
      }
    }
  }

  .home_btn {
    width           : 69px;
    height          : 66px;
    border          : none;
    border-radius   : 15px;
    background-color: #F5C242;
    display         : flex;
    align-items     : center;
    justify-content : center;
    position        : absolute;
    top             : 47px;
    left            : 34px;

    &>img {
      width : 46px;
      height: 46px;
    }
  }
}

.modal {
  width           : 298px;
  height          : 222px;
  border          : none;
  border-radius   : 30px;
  background-color: var(--red);
  position        : absolute;
  left            : 50%;
  top             : 154px;
  transform       : translate(-50%, 0);

  .lock_icon_container {
    display        : flex;
    justify-content: center;
    margin-top     : 8px;

    .lock_icon {
      width           : 74px;
      height          : 74px;
      border-radius   : 50%;
      border          : none;
      background-color: #F5C242;
      font-size       : 46px;
      color           : #FFF;
      display         : flex;
      align-items     : center;
      justify-content : center;
    }
  }

  .text_container {
    width          : 100%;
    display        : flex;
    justify-content: center;
    margin-top     : 2px;

    .text {
      width         : 266px;
      font-family   : Poppins;
      font-size     : 9px;
      font-style    : normal;
      font-weight   : 600;
      line-height   : 14px;
      letter-spacing: 0em;
      text-align    : center;
      color         : #FFF;
    }

  }

  .input_container {
    width          : 100%;
    display        : flex;
    justify-content: center;
    margin-top     : 11px;

    &>input {
      width           : 266px;
      height          : 33px;
      border          : none;
      border-radius   : 11px;
      box-shadow      : 0px 4px 5px 0px #00000040 inset;
      background-color: #FFF;
      font-family     : Poppins;
      font-size       : 9px;
      font-style      : normal;
      font-weight     : 600;
      line-height     : 33px;
      letter-spacing  : 0em;
      text-align      : center;
      color           : #000;
      outline         : none;
    }
  }

  .confirm_btn_container {
    width          : 100%;
    display        : flex;
    justify-content: center;
    margin-top     : 14px;

    &>button {
      width           : 167px;
      height          : 30px;
      border          : none;
      border-radius   : 16px;
      box-shadow      : 0px 4px 20px -8px #00000040;
      background-color: #F5C242;
      font-family     : Poppins;
      font-size       : 12px;
      font-style      : normal;
      font-weight     : 600;
      line-height     : 30px;
      letter-spacing  : 0em;
      text-align      : center;
      color           : #FFF;

      &:hover {
        cursor: pointer;
      }
    }
  }
}

