.main {
  width: 100%;
  height: 100%;
  position: relative;

  .message_card {
    width        : 298px;
    height       : 222px;
    border       : none;
    border-radius: 30px;
    box-shadow   : 0px 4px 4px 0px #00000040;
    background   : #FFF;

    position: absolute;
    bottom     : 134px;
    left   : 50%;
    transform: translateX(-50%);

    .rel_container {
      width: 100%;
      height: 100%;
      position: relative;

      display: flex;
      justify-content: center;
      align-items: center;

      .message {

        width: 100%;
        padding: 0 5px;

        font-family   : Poppins;
        font-size     : 20px;
        font-style    : normal;
        font-weight   : 500;
        line-height   : 26px;
        letter-spacing: 0.02em;
        text-align    : center;
        color: #039A00;
      }

      .close {
        pointer-events: all;
        width          : 36px;
        height         : 36px;
        border         : none;
        border-radius  : 50%;
        background     : var(--red);
        display        : flex;
        justify-content: center;
        align-items    : center;
        box-shadow     : 0px 4px 4px 0px #00000040;

        position: absolute;
        top: 0;
        right: -69px;

        &:hover {
          cursor: pointer;
        }

        .close_btn {
          color    : #FFF;
          font-size: 30px;
        }
      }
    }
  }
}