.main {
  overflow        : hidden;
  position        : relative;
  width           : 100%;
  height          : 100%;
  background-color: #FFF;

  .ranking {
    width           : 100%;
    height          : 100%;
    overflow        : hidden;
    position        : relative;
    background-color: transparent;

    .ranking_container {
      height        : 100%;
      position      : absolute;
      top           : 0;
      right         : 0;
      padding       : 40px 0;
      pointer-events: all;
      overflow-y    : auto;
      overflow-x    : hidden;

      .header {
        width       : 100%;
        padding-left: 27px;
        display     : flex;
        align-items : center;

        .title {
          font-family   : Poppins;
          font-size     : 30px;
          font-style    : normal;
          font-weight   : 700;
          line-height   : 34px;
          letter-spacing: 0.02em;
          text-align    : left;
          color         : var(--red);
        }

        &>img {
          width       : 28px;
          height      : 32px;
          margin-right: 18px;
        }
      }

      .ranking_table {
        width           : 100%;
        // height       : 86%;
        margin-top      : 30px;
        margin-bottom   : 27px;

        .table_header {
          width         : 100%;
          padding-left  : 126px;
          padding-bottom: 32px;
          display       : flex;

          font-family   : Poppins;
          font-size     : 20px;
          font-style    : normal;
          font-weight   : 600;
          line-height   : 30px;
          letter-spacing: 0em;
          text-align    : left;

          color: var(--red);

          .name {
            width: 160px;
          }

          .unit {
            width: 150px;
          }

          .office {
            width: 128px;
          }

          .time {
            width: 136px;
          }

          .achivement {
            width: 181px;
          }
        }

        .table_content {
          width        : 995px;
          height       : 100%;
          // overflow-y: auto;

          // &::-webkit-scrollbar {
          //   width: 8px;
          // }

          // &::-webkit-scrollbar-track {
          //   background-color: unset;
          //   border-radius   : 100px;
          // }

          // &::-webkit-scrollbar-thumb {
          //   background-color: transparent;
          //   border          : 3px solid rgba(0, 0, 0, 0);
          //   background-clip : padding-box;
          //   border-radius   : 6px;
          // }

          // &:hover::-webkit-scrollbar-thumb {
          //   background-color: rgba(177, 34, 34, 1);
          // }
        }

      }

      &::-webkit-scrollbar {
        width: 8px;
      }

      &::-webkit-scrollbar-track {
        background-color: unset;
        border-radius   : 100px;
      }

      &::-webkit-scrollbar-thumb {
        background-color: transparent;
        border          : 3px solid rgba(0, 0, 0, 0);
        background-clip : padding-box;
        border-radius   : 6px;
      }

      &:hover::-webkit-scrollbar-thumb {
        background-color: rgba(177, 34, 34, 1);
      }

    }
  }
}