.progress_content {
  width                        : 100%;
  border                       : none;
  border-radius                : 30px;
  border-bottom-left-radius    : 0;
  border-bottom-right-radius   : 0;
  background-color             : #FFF;
  position                     : relative;
  // height                    : 200px;

  .progress_content_container {
    width         : 100%;
    padding-top   : 30px;
    padding-right : 30px;
    padding-left  : 20px;
    padding-bottom: 50px;

    .header {
      width  : 100%;
      display: flex;

      .title {
        margin-left   : 5px;
        font-family   : Poppins;
        font-size     : 24px;
        font-style    : normal;
        font-weight   : 700;
        line-height   : 36px;
        letter-spacing: 0em;
        text-align    : left;
        color         : var(--red);
      }

      .progress_search {
        width          : 100%;
        margin-top     : 4px;
        display        : flex;
        justify-content: flex-end;

        .search_input_container {
          width           : 480px;
          height          : 36px;
          border          : 1px solid #B4B4B4;
          border-radius   : 10px;
          padding         : 7px 21px 7px 34px;
          background-color: transparent;
          display         : flex;
          align-items     : center;

          .search_input {
            width           : 100%;
            height          : 100%;
            border          : none;
            background-color: transparent;
            color           : #535353;
            outline         : none;
          }

          .input_icon {
            width : 19px;
            height: 19px;

            .search_icon {
              width : 100%;
              height: 100%;
            }
          }
        }
      }
    }

    .content {
      width     : 100%;
      display   : flex;
      margin-top: 84px;

      .left_content {
        width      : 455px;
        position   : relative;
        padding-top: 26px;

        .rel_container {
          width          : 455px;
          display        : flex;
          justify-content: center;

          .model_img {
            // width  : 284px;
            width: 155px;
            height : 511px;
            z-index: 1;

            &>img {
              width : 100%;
              height: 100%;
            }
          }
        }



        .effect {
          width     : 455px;
          height    : 455px;
          background: radial-gradient(50% 50% at 50% 50%, rgba(250, 218, 120, 0.35) 15.1%, rgba(255, 192, 0, 0) 100%);
          position  : absolute;
          top       : 42px;
          left      : 0;
        }
      }

      .right_content {
        width       : 100%;
        margin-right: -8px;
        position    : relative;

        &::after {
          position         : absolute;
          content          : "";
          left             : 0px;
          top              : 0px;
          height           : 100%;
          width            : 100%;
          background       : linear-gradient(transparent 75%, white 100%);
          pointer-events   : none;
          // border-radius : 14px;
        }

        .title {
          font-family   : Poppins;
          font-size     : 20px;
          font-style    : normal;
          font-weight   : 500;
          line-height   : 26px;
          letter-spacing: 0.02em;
          text-align    : left;
          color         : var(--grey);
          margin-bottom : 30px;
        }

        .achievement_item_content {
          width        : 100%;
          height       : 471px;
          overflow-y   : auto;
          padding-right: 8px;
          position     : relative;


          .no_result {
            position   : absolute;
            left       : 50%;
            top        : 40px;
            transform  : translateX(-50%);
            font-size  : 30px;
            color      : var(--red);
            font-weight: 600;
          }



          &::-webkit-scrollbar {
            width: 8px;
          }

          &::-webkit-scrollbar-track {
            background-color: unset;
            border-radius   : 100px;
          }

          &::-webkit-scrollbar-thumb {
            background-color   : transparent;
            // background-color: rgba(177, 34, 34, 1);
            border             : 3px solid rgba(0, 0, 0, 0);
            background-clip    : padding-box;
            border-radius      : 6px;
          }

          &:hover::-webkit-scrollbar-thumb {
            background-color: rgba(177, 34, 34, 1);
          }
        }
      }
    }

    .certification {
      width      : 100%;
      margin-top : 40px;
      margin-left: 10px;

      .title {
        font-family   : Poppins;
        font-size     : 20px;
        font-style    : normal;
        font-weight   : 500;
        line-height   : 26px;
        letter-spacing: 0.02em;
        text-align    : left;
        margin-bottom : 20px;

        color: var(--grey);
      }
    }
  }
}