.university_content {
  width                        : 100%;
  border                       : none;
  border-radius                : 30px;
  border-bottom-left-radius    : 0;
  border-bottom-right-radius   : 0;
  background-color             : #FFF;
  position                     : relative;
  // height                    : 200px;

  .tab_btn_container {
    position : absolute;
    top      : 0;
    left     : 50%;
    transform: translate(-50%, -100%);

    .tab_btn {
      display        : flex;
      justify-content: center;

      .tab_first_btn {
        height                : 27px;
        width                 : 187px;
        border-top-left-radius: 50px;
        border                : none;
      }


      .tab_second_btn {
        height                 : 27px;
        width                  : 187px;
        border-top-right-radius: 50px;
        border                 : none;
      }

      .tab_first_btn:hover,
      .tab_second_btn:hover {
        cursor: pointer;
      }

      .w_back {
        background-color: #FFF;
        color           : #E53222;
      }

      .r_back {
        background-color: #EE8885;
        color           : #FFF;
      }
    }
  }

  .university_detail {
    width         : 100%;
    padding-top   : 30px;
    padding-right : 30px;
    padding-left  : 30px;
    padding-bottom: 127px;

    .detail_info {
      width: 100%;

      .content_title {
        width: 100%;

        .content {
          font-family   : Poppins;
          font-size     : 24px;
          font-style    : normal;
          font-weight   : 700;
          line-height   : 36px;
          letter-spacing: 0em;
          text-align    : left;
          color         : var(--red);
        }

        .title {
          font-family   : Poppins;
          font-size     : 18px;
          font-style    : normal;
          font-weight   : 400;
          line-height   : 27px;
          letter-spacing: 0em;
          text-align    : left;
          color         : var(--grey);
          margin-top    : 2px;

          &>a {
            color: var(--grey);
          }
        }
      }

      .info {
        width          : 100%;
        margin-top     : 48px;
        display        : flex;
        justify-content: flex-start;

        .left_info {
          // width: 561px;
          width: 58%;

          .left_header {
            width: 561px;

            .info_title {
              font-family   : Poppins;
              font-size     : 30px;
              font-style    : normal;
              font-weight   : 700;
              line-height   : 41px;
              letter-spacing: 0.02em;
              text-align    : left;
              color         : var(--red);
            }

            .info_subtitle {
              font-family   : Poppins;
              font-size     : 20px;
              font-style    : normal;
              font-weight   : 500;
              line-height   : 26px;
              letter-spacing: 0.02em;
              text-align    : left;
              color         : var(--grey);
            }
          }

          .description {
            width     : 100%;
            margin-top: 40px;

            .description_title {
              //styleName: H3;
              font-family   : Poppins;
              font-size     : 20px;
              font-style    : normal;
              font-weight   : 500;
              line-height   : 26px;
              letter-spacing: 0.02em;
              text-align    : left;
              color         : var(--grey);
            }

            .description_content {
              width         : 100%;
              height        : 140px;
              margin-top    : 16px;
              font-family   : Poppins;
              font-size     : 14px;
              font-style    : normal;
              font-weight   : 400;
              line-height   : 21px;
              letter-spacing: 0.02em;
              text-align    : left;
              color         : var(--grey);
              position      : relative;
              overflow      : hidden;

              &::after {
                position         : absolute;
                content          : "";
                left             : 0px;
                top              : 0px;
                height           : 100%;
                width            : 100%;
                background       : linear-gradient(transparent, white);
                pointer-events   : none;
                // border-radius : 14px;
              }
            }

            .description_all_content {
              width         : 100%;
              margin-top    : 16px;
              font-family   : Poppins;
              font-size     : 14px;
              font-style    : normal;
              font-weight   : 400;
              line-height   : 21px;
              letter-spacing: 0.02em;
              text-align    : left;
              color         : var(--grey);
              position      : relative;
            }
          }

          .more_view {
            width: 100%;

            &>button {
              width           : 100%;
              height          : 48px;
              border          : 1px solid #E53222;
              border-radius   : 10px;
              background-color: #FFF;
              font-family     : Poppins;
              font-size       : 14px;
              font-style      : normal;
              font-weight     : 400;
              line-height     : 21px;
              letter-spacing  : 0.02em;
              text-align      : center;
              color           : var(--red);
            }

            &>button:hover {
              cursor: pointer;
            }
          }

          .more_info {
            width     : 100%;
            margin-top: 46px;
            display   : flex;

            .left_more_info {
              display: flex;

              .more_info_img {
                width : 40px;
                height: 40px;

                &>img {
                  width : 40px;
                  height: 40px;
                }
              }

              .more_info_content {
                width      : 100%;
                margin-left: 16px;

                .more_info_content_title {
                  width         : 100%;
                  //styleName: H3;
                  font-family   : Poppins;
                  font-size     : 20px;
                  font-style    : normal;
                  font-weight   : 500;
                  line-height   : 26px;
                  letter-spacing: 0.02em;
                  text-align    : left;
                  color         : var(--red);
                }

                .content {
                  width         : 100%;
                  margin-top    : 4px;
                  font-family   : Poppins;
                  font-size     : 20px;
                  font-style    : normal;
                  font-weight   : 500;
                  line-height   : 26px;
                  letter-spacing: 0.02em;
                  text-align    : left;
                  color         : #B4B4B4;
                }
              }
            }

            .right_more_info {
              margin-left: 42px;
              display    : flex;

              .more_info_img {
                width : 40px;
                height: 40px;

                &>img {
                  width : 40px;
                  height: 40px;
                }
              }

              .more_info_content {
                width      : 100%;
                margin-left: 16px;

                .more_info_content_title {
                  width         : 100%;
                  font-family   : Poppins;
                  font-size     : 20px;
                  font-style    : normal;
                  font-weight   : 500;
                  line-height   : 26px;
                  letter-spacing: 0.02em;
                  text-align    : left;
                  color         : var(--red);
                }

                .content {
                  width         : 100%;
                  margin-top    : 4px;
                  font-family   : Poppins;
                  font-size     : 20px;
                  font-style    : normal;
                  font-weight   : 500;
                  line-height   : 26px;
                  letter-spacing: 0.02em;
                  text-align    : left;
                  color         : #B4B4B4;
                }
              }
            }
          }
        }

        .right_info {
          // width      : 386px;
          width       : 42%;
          padding-left: 32px;

          .video_container {
            width: 100%;
          }

          .video_info {
            width         : 100%;
            margin-top    : 20px;
            text-align    : center;
            font-family   : Poppins;
            font-size     : 12px;
            font-style    : normal;
            font-weight   : 400;
            line-height   : 14px;
            letter-spacing: 0em;
            color         : #B4B4B4;
          }

          .now_btn {
            width     : 100%;
            margin-top: 28px;

            & button {
              width           : 100%;
              height          : 48px;
              background-color: #F5C242;
              border          : none;
              border-radius   : 10px;
              font-family     : Poppins;
              font-size       : 20px;
              font-style      : normal;
              font-weight     : 500;
              line-height     : 26px;
              letter-spacing  : 0.02em;
              text-align      : center;
              color           : #FFF;

            }

            & button:hover {
              cursor: pointer;
            }
          }

          .later_btn {
            width     : 100%;
            margin-top: 16px;

            &>button {
              width           : 100%;
              height          : 48px;
              border          : 1px solid #535353;
              border-radius   : 10px;
              background-color: #FFF;
              //styleName: H3;
              font-family     : Poppins;
              font-size       : 20px;
              font-style      : normal;
              font-weight     : 500;
              line-height     : 26px;
              letter-spacing  : 0.02em;
              text-align      : center;
              color           : var(--grey);
            }

            &>button:hover {
              cursor: pointer;
            }
          }

        }
      }
    }

    .want {
      width     : 100%;
      margin-top: 96px;

      .want_title {
        font-family   : Poppins;
        font-size     : 20px;
        font-style    : normal;
        font-weight   : 500;
        line-height   : 26px;
        letter-spacing: 0.02em;
        text-align    : left;
        color         : var(--grey);
        margin-bottom : 17px;
      }

      .want_item {
        width  : 100%;
        display: flex;

        .check_icon {
          width : 27px;
          height: 27px;

          * {
            color: var(--grey);
          }
        }

        .want_content {
          margin-left   : 16px;
          font-family   : Poppins;
          font-size     : 16px;
          font-style    : normal;
          font-weight   : 400;
          line-height   : 24px;
          letter-spacing: 0.02em;
          text-align    : left;
          color         : var(--grey);
        }
      }
    }

    .video_list {
      width     : 100%;
      margin-top: 96px;
    }

    .forum {
      width     : 100%;
      margin-top: 96px;

      .forum_title {
        font-family   : Poppins;
        font-size     : 20px;
        font-style    : normal;
        font-weight   : 500;
        line-height   : 26px;
        letter-spacing: 0.02em;
        text-align    : left;
        color         : var(--grey);
      }

      .forum_container {
        width: 100%;

        .more_view_btn {
          width           : 100%;
          height          : 38px;
          background-color: #FFF;
          border          : 1px solid #E53222;
          border-radius   : 10px;
          font-family     : Poppins;
          font-size       : 14px;
          font-style      : normal;
          font-weight     : 400;
          line-height     : 21px;
          letter-spacing  : 0.02em;
          text-align      : center;
          color           : var(--red);
          transition      : color 0.5s,
            background-color 0.5s;
          transition-timing-function: ease;

          &:hover {
            cursor          : pointer;
            background-color: var(--red);
            color           : #FFF;
          }
        }
      }


    }

    .related_course {
      width     : 100%;
      margin-top: 96px;

      .title {
        font-family   : Poppins;
        font-size     : 20px;
        font-style    : normal;
        font-weight   : 500;
        line-height   : 26px;
        letter-spacing: 0.02em;
        text-align    : left;
        color         : var(--grey);
        margin-bottom : 20px;


      }

      .content {
        width: 100%;
      }
    }

  }

}