.certification_item {
    width      : 100%;
    padding-top: 19px;

    .header {
        width          : 100%;
        padding-left   : 35px;
        padding-right  : 27px;
        display        : flex;
        align-items    : center;
        justify-content: space-between;
        margin-bottom  : 24px;

        .link_address {
            font-family   : Poppins;
            font-size     : 14px;
            font-style    : normal;
            font-weight   : 500;
            line-height   : 21px;
            letter-spacing: 0.02em;
            text-align    : left;
            color         : var(--grey);

            &>a {
                font-family   : Poppins;
                font-size     : 14px;
                font-style    : normal;
                font-weight   : 400;
                line-height   : 21px;
                letter-spacing: 0.02em;
                text-align    : left;
                color         : var(--grey);

                text-decoration: underline;
            }
        }

        .social_container {
            display    : flex;
            align-items: center;

            .social_text {
                font-family   : Poppins;
                font-size     : 14px;
                font-style    : normal;
                font-weight   : 500;
                line-height   : 21px;
                letter-spacing: 0.02em;
                text-align    : left;
                color         : var(--grey);
            }

            .icons {
                display    : flex;
                align-items: center;

                .social_icon {
                    margin-left: 13px;
                    width      : 28px;
                    height     : 28px;

                    &>img {
                        width : 100%;
                        height: 100%;
                    }

                    &:hover {
                        cursor: pointer;
                    }
                }
            }
        }
    }

    .link_content {
        width        : 100%;
        height       : 137px;
        padding-left : 35px;
        padding-right: 27px;
        position     : relative;

        .iframe_link {
            width : 100%;
            height: 100%;
            border: none;
        }

        &::after {
            position         : absolute;
            content          : "";
            left             : 0px;
            top              : 0px;
            height           : 100%;
            width            : 100%;
            background       : linear-gradient(white 0%, transparent 50%);
            pointer-events   : none;
            // border-radius : 14px;
        }
    }
}