.video {
    display       : flex;
    align-items   : center;
    height        : 100%;
    position      : relative;
    width         : 100%;
    // overflow   : hidden;

    .source_container {
        width          : 100%;
        position       : relative;
        display        : flex;
        justify-content: center;

        .source_hover_img {
            position        : absolute;
            left            : 50%;
            top             : 0;
            transform       : translate(-50%, 0);
            // width        : 100%;
            height          : 100%;
            background      : #161616B2;
            border-radius   : 25px;
        }

        .stop_btn_container {
            width                     : 37px;
            height                    : 37px;
            position                  : absolute;
            left                      : 50%;
            top                       : 50%;
            transform                 : translate(-50%, -50%);
            border                    : none;
            border-radius             : 50%;
            background-color          : #FFF;
            display                   : flex;
            justify-content           : center;
            align-items               : center;
            transition                : background-color 0.2s;
            transition-timing-function: ease;

            * {
                transition                : color 0.2s;
                transition-timing-function: ease;
                transform                 : translate(7%, 0);
                color                     : #733E45;
            }
        }

        .stop_btn_container:hover {
            cursor          : pointer;
            background-color: #733E45;

            * {
                color: #FFF;
            }
        }

        .source {
            border-radius: 25px;
            position     : relative;

            // width        : 100%;
            // height          : 350px;
        }

        .progress {
            &::-webkit-media-controls-timeline {
                display: block;
            }
        }

        .no_progress {
            &::-webkit-media-controls-timeline {
                display: none;
            }
        }

    }





}