.chatlist {
    // max-height            : 600px;
    height           : 600px;
    width            : 254px;
    pointer-events   : all;
    // position      : absolute;
    // right         : 0;
    // bottom        : 0;


    .chatlist_container {
        width                  : 100%;
        height                 : 520px;
        position               : relative;
        padding-top            : 12px;
        padding-right          : 12px;
        background             : #E53222;
        border                 : none;
        border-top-left-radius : 13px;
        border-top-right-radius: 13px;
        box-shadow             : 6px 4px 5px 0px #0000001A;

        .chatlist_user_info_container {
            width       : 100%;
            padding-left: 21px;
            max-height  : 96px;
            position    : relative;

            .close_btn {
                width: 9px;
                height: 9px;
                position: absolute;
                right: 2px;
                top: -5px;

                .close_icon {
                    color: #FFF;
                    font-size: 14px;
                }

                &:hover {
                    cursor: pointer;
                }
            }

            .chatlist_user_info {
                display        : flex;
                justify-content: flex-start;
                align-items    : center;

                .user_avatar_container {
                    width           : 48px;
                    height          : 48px;
                    border-radius   : 50%;
                    border          : none;
                    background-color: #F5C242;

                    .user_avatar {
                        width        : 100%;
                        height       : 100%;
                        border-radius: 50%;
                        border       : none;
                    }
                }

                .user_name {
                    margin-left: 12px;
                    font-size  : 14px;
                    font-style : normal;
                    font-weight: 500;
                    line-height: 21px;
                    text-align : left;
                    color      : #FFF;
                }
            }

            .chatlist_search_container {
                margin-top   : 24px;
                width        : 100%;
                height       : 24px;
                padding-right: 6px;

                .search_input_container {
                    background   : #B02418;
                    border       : none;
                    border-radius: 4px;
                    display      : flex;
                    padding      : 3.2px 5px;
                    height       : 100%;

                    .search_input {
                        background : transparent;
                        border     : none;
                        outline    : none;
                        width      : 100%;
                        font-family: Poppins;
                        color      : #FFF;
                    }

                    .search_input_icon {
                        width: 17.4px;
                        width: 17.4px;

                        &>img {
                            width : 100%;
                            height: 100%;
                        }
                    }
                }
            }
        }

        .chatlist_content {
            width     : 100%;
            height    : 412px;
            overflow-x: hidden;
            overflow-y: auto;

            .chatlist_member_list_container {
                width        : 100%;
                padding      : 8px 6px 0 25px;
                margin-bottom: 24px;

                .member_list_title {
                    font-size     : 14px;
                    font-style    : normal;
                    font-weight   : 500;
                    line-height   : 21px;
                    letter-spacing: 0em;
                    color         : #FFF;
                }

                .member_list_container {
                    margin-top: 12px;
                }
            }

            .chatlist_divider_container {

                width  : 100%;
                height : 1px;
                padding: 0 6px 0 16px;

                .chatlist_divider {
                    width           : 100%;
                    height          : 100%;
                    background-color: #FFF;
                    border-radius   : 1px;
                }
            }

            .chatlist_group_list_container {
                width  : 100%;
                padding: 16px 6px 0 25px;

                .chatlist_group_list_title {
                    font-family   : Poppins;
                    font-size     : 14px;
                    font-style    : normal;
                    font-weight   : 500;
                    line-height   : 21px;
                    letter-spacing: 0em;
                    text-align    : left;
                    color         : #FFF;
                }

                .chatlist_group_list {
                    padding-top: 12px;
                    width      : 100%;
                }
            }

            &::-webkit-scrollbar {
                width: 4px;
            }

            &::-webkit-scrollbar-track {
                background-color: transparent;
                border-radius   : 100px;
            }

            &::-webkit-scrollbar-thumb {
                border-radius   : 100px;
                background-clip : content-box;
                background-color: rgba(255, 255, 255, 0.1);
            }

        }

        // .chatlist_content:hover {
        //     overflow-y: auto;
        // }

    }

    .chatlist_bottom_container {
        width   : 100%;
        height  : 80px;
        position: relative;

        .chatlist_bottom_back_img {
            position     : absolute;
            left         : 0;
            top          : 0;
            width        : 100%;
            height       : 100%;
            // box-shadow: 6px 4px 5px 0px #0000001A;
        }

        .chatlist_exit_btn_container {
            position: absolute;
            right   : 0;
            bottom  : 0;

            .chatlist_exit_btn {
                display        : flex;
                justify-content: center;
                align-items    : center;
                width          : 52px;
                height         : 52px;
                border         : none;
                border-radius  : 50%;
                background     : #F5C242;
                padding        : 0;
                color          : #FFF;
                box-shadow     : 6px 4px 5px 0px #0000001A;
            }

            .chatlist_exit_btn:hover {
                cursor: pointer;
            }
        }
    }
}