.send {
    width       : 100%;
    min-height  : 58px;
    margin-top  : 10px;
    padding-left: 27px;

    .send_container {
        width : 100%;
        height: 100%;

        .message_header {
            width: 100%;

            display        : flex;
            justify-content: flex-end;
            align-items    : center;
            margin-bottom  : 7px;

            .message_header_img {
                width           : 20px;
                height          : 20px;
                border          : none;
                border-radius   : 50%;
                background-color: #F5C242;
            }

            .message_header_name {
                font-family   : Poppins;
                font-size     : 8px;
                font-style    : normal;
                font-weight   : 500;
                line-height   : 12px;
                letter-spacing: 0em;
                text-align    : left;
                color         : #FFF;
                margin-left   : 6.5px;
                margin-right  : 15px;
            }
        }

        .message_content {
            width        : 100%;
            border       : none;
            border-radius: 7px;
            background   : #EEEEEE;
            box-shadow   : 0px 4px 4px 0px #00000040;
            color        : #B02418;
            position     : relative;
            padding      : 8px 18px;
            min-height   : 31px;

            .message {
                font-family   : Poppins;
                font-size     : 10px;
                font-style    : normal;
                font-weight   : 500;
                line-height   : 15px;
                letter-spacing: 0em;
                text-align    : left;
                color         : #B02418;
                position      : relative;

                .time {
                    position      : absolute;
                    right         : 0;
                    bottom        : 0;
                    transform     : translate(0, 90%);
                    font-family   : Poppins;
                    font-size     : 7px;
                    font-style    : normal;
                    font-weight   : 500;
                    line-height   : 11px;
                    letter-spacing: 0em;
                    text-align    : left;
                    color         : #B02418;
                }
            }

            .triangle {
                position     : absolute;
                bottom       : 0;
                right        : 0;
                width        : 0;
                height       : 0;
                border-left  : 10px solid transparent;
                border-right : 13px solid transparent;
                border-bottom: 13px solid #EEEEEE;
                transform    : translate(5.5px, 0);
            }
        }
    }
}

