.university_content {
  width                        : 100%;
  // height                       : 100%;
  border                       : none;
  border-radius                : 30px;
  border-bottom-left-radius    : 0;
  border-bottom-right-radius   : 0;
  background-color             : #FFF;
  position                     : relative;
  // height                    : 200px;

  .tab_btn_container {
    position : absolute;
    top      : 0;
    left     : 50%;
    transform: translate(-50%, -100%);

    .tab_btn {
      display        : flex;
      justify-content: center;

      .tab_first_btn {
        height                : 27px;
        width                 : 187px;
        border-top-left-radius: 50px;
        border                : none;
      }


      .tab_second_btn {
        height                 : 27px;
        width                  : 187px;
        border-top-right-radius: 50px;
        border                 : none;
      }

      .tab_first_btn:hover,
      .tab_second_btn:hover {
        cursor: pointer;
      }

      .w_back {
        background-color: #FFF;
        color           : #E53222;
      }

      .r_back {
        background-color: #EE8885;
        color           : #FFF;
      }
    }
  }

  .university_instructor {
    width  : 100%;
    padding: 30px;

    .no_result {
      position   : absolute;
      left       : 50%;
      top        : 95px;
      transform  : translateX(-50%);
      font-size  : 30px;
      color      : var(--red);
      font-weight: 600;
    }

    .header {
      width          : 100%;
      display        : flex;
      justify-content: space-between;

      .title {
        font-family   : Poppins;
        font-size     : 24px;
        font-style    : normal;
        font-weight   : 700;
        line-height   : 36px;
        letter-spacing: 0em;
        text-align    : left;
        color         : var(--red);
      }

      .search_input_container {
        width           : 480px;
        height          : 36px;
        border          : 1px solid #B4B4B4;
        border-radius   : 10px;
        padding         : 7px 21px 7px 34px;
        background-color: transparent;
        display         : flex;
        align-items     : center;

        .search_input {
          width           : 100%;
          height          : 100%;
          border          : none;
          background-color: transparent;
          color           : #535353;
          outline         : none;
        }

        .input_icon {
          width : 19px;
          height: 19px;

          .search_icon {
            width : 100%;
            height: 100%;
          }
        }
      }
    }

    .market {
      width     : 100%;
      margin-top: 56px;

      &:nth-child(2) {
        margin-top: 42px;
      }

      .title {
        font-family   : Poppins;
        font-size     : 20px;
        font-style    : normal;
        font-weight   : 500;
        line-height   : 26px;
        letter-spacing: 0.02em;
        text-align    : left;
        color         : var(--grey);
      }

      .content {
        width     : 100%;
        margin-top: 14px;
      }
    }



  }

}


