.main {
  overflow            : hidden;
  position            : relative;
  width               : 100%;
  height              : 100%;
  // background-image : url('../../assets/image/back/home_back.png');
  // background-repeat: no-repeat;
  // background-size  : 100% 100%;

  .login {
    width           : 100%;
    height          : 100%;
    overflow        : none;
    position        : relative;
    background-color: transparent;
    display         : flex;
    justify-content : center;
    align-items     : center;

    .login_card {
      min-height         : 454px;
      width              : 52%;
      // height          : 76%;
      background-color   : #fff;
      border             : none;
      border-radius      : 30px;
      padding            : 66px 70px;
      pointer-events     : all;

      .login_title {
        width         : 100%;
        font-family   : Poppins;
        font-size     : 29px;
        font-style    : normal;
        font-weight   : 700;
        line-height   : 33px;
        letter-spacing: 0em;
        text-align    : center;
        color         : #000;
      }

      .login_subtitle {
        width         : 100%;
        font-family   : Poppins;
        font-size     : 16px;
        font-style    : normal;
        font-weight   : 400;
        line-height   : 18px;
        letter-spacing: 0em;
        text-align    : center;
        color         : #000;
        margin-top    : 6px;
      }

      .login_input {
        width        : 100%;
        border       : 1px solid #C4C4C4;
        border-radius: 10px;
        max-height   : 55px;
        text-align   : center;
        color        : #000;
        padding      : 5px 0;
        font-size    : 18px;
        line-height  : 48px;
        font-weight  : 400;
        font-family  : Poppins;
        outline      : none;
      }

      .login_input.email {
        margin-top: 25px;
      }

      .login_input.password {
        margin-top: 15px;
      }

      .login_input.error {
        border-color: var(--red);
      }

      .login_forgot_btn_container {
        width          : 100%;
        display        : flex;
        justify-content: center;
        margin-top     : 25px;

        .login_forgot_btn {
          font-size      : 14px;
          font-style     : normal;
          font-weight    : 400;
          line-height    : 16px;
          letter-spacing : 0em;
          text-align     : center;
          color          : #000;
          text-decoration: underline;
        }
      }

      .login_confirm_btn_container {

        margin-top     : 20px;
        display        : flex;
        justify-content: center;

        .login_confirm_btn {
          width           : 55%;
          padding         : 14px 0;
          background-color: #F5C242;
          max-height      : 55px;
          border-radius   : 10px;
          font-size       : 18px;
          line-height     : 27px;
          font-weight     : 600;
          border          : none;
          color           : #FFF;
        }

        .login_confirm_btn:hover {
          cursor: pointer;
        }

        // a {
        //   display        : flex;
        //   justify-content: center;
        //   width          : 100%;

        //   .login_confirm_btn {
        //     width           : 55%;
        //     padding         : 14px 0;
        //     background-color: #F5C242;
        //     max-height      : 55px;
        //     border-radius   : 10px;
        //     font-size       : 18px;
        //     line-height     : 27px;
        //     font-weight     : 600;
        //     border          : none;
        //     color           : #FFF;
        //   }

        //   .login_confirm_btn:hover {
        //     cursor: pointer;
        //   }
        // }
      }
    }
  }
}

@media (max-width: 1280px) {
  .login {

    .login_card {
      width: 60%;


    }
  }
}