.main {
  overflow        : hidden;
  position        : relative;
  width           : 100%;
  height          : 100%;
  background-color: #FFF;

  .schedule {
    width           : 100%;
    height          : 100%;
    overflow        : hidden;
    position        : relative;
    background-color: transparent;

    .schedule_container {
      height        : 100%;
      position      : absolute;
      top           : 0;
      right         : 0;
      pointer-events: all;
      padding       : 72px 40px 68px 0;
      display       : flex;

      .left_section {
        width : 468px;
        height: 100%;

        .header {
          width         : 468px;
          font-family   : Poppins;
          font-size     : 24px;
          font-style    : normal;
          font-weight   : 700;
          line-height   : 36px;
          letter-spacing: 0em;
          text-align    : left;
          color         : var(--red);
        }

        .subtitle {
          width         : 100%;
          font-family   : Poppins;
          font-size     : 20px;
          font-style    : normal;
          font-weight   : 500;
          line-height   : 26px;
          letter-spacing: 0.02em;
          text-align    : left;
          color         : var(--grey);
          margin-top    : 50px;
        }

        .calendar_container {
          width              : 100%;
          // max-height      : 344px;
          margin-top         : 10px;
          // border       : 1px solid #a0a096;
          // border-radius: 24px;
        }
      }

      .right_section {
        padding-left: 20px;
        width       : 100%;
        height      : 100%;

        .header {
          width: 100%;
          display: flex;
          justify-content: flex-end;

          .search_input_container {
            width           : 480px;
            height          : 36px;
            border          : 1px solid #B4B4B4;
            border-radius   : 10px;
            padding         : 7px 21px 7px 34px;
            background-color: transparent;
            display         : flex;
            align-items     : center;

            .search_input {
              width           : 100%;
              height          : 100%;
              border          : none;
              background-color: transparent;
              color           : #535353;
              outline         : none;
            }

            .input_icon {
              width : 19px;
              height: 19px;

              .search_icon {
                width : 100%;
                height: 100%;
              }
            }
          }
        }

        .subtitle {
          font-family   : Poppins;
          font-size     : 20px;
          font-style    : normal;
          font-weight   : 500;
          line-height   : 26px;
          letter-spacing: 0.02em;
          text-align    : left;
          color         : var(--grey);
          margin-top    : 50px;
        }

        .rel_container {
          width     : 100%;
          margin-top: 10px;
          overflow  : hidden;
          position  : relative;

          &::after {
            position      : absolute;
            content       : "";
            left          : 0px;
            top           : 0px;
            height        : 100%;
            width         : 100%;
            background    : linear-gradient(transparent 75%, white 100%);
            pointer-events: none;
          }

          .schedule_items {
            width     : 100%;
            height    : 100%;
            overflow-x: hidden;
            overflow-y: auto;
            position  : relative;

            .no_result {
              position   : absolute;
              left       : 50%;
              top        : 40px;
              transform  : translateX(-50%);
              font-size  : 30px;
              color      : var(--red);
              font-weight: 600;
            }



            &::-webkit-scrollbar {
              width: 0;
            }

            &::-webkit-scrollbar-track {
              // background-color: rgb(245, 81, 17);
              // background-color: transparent;
              border-radius: 100px;
            }

            &::-webkit-scrollbar-thumb {
              border-radius      : 100px;
              background-clip    : content-box;
              // background-color: rgba(255, 255, 255, 0.1);
              background-color   : white;
            }
          }
        }
      }
    }

  }
}