.complete_modal {
    position        : fixed;
    top             : 0;
    left            : 0;
    background-color: #00000040;
    display         : flex;
    justify-content : center;
    align-items     : center;
    z-index         : 10;

    .card {
        width           : 563px;
        height          : 522px;
        border          : none;
        border-radius   : 30px;
        background-color: #FFF;
        padding         : 116px 40px 32px 40px;
        position        : relative;
        pointer-events  : all;

        .close_btn {
            position : absolute;
            top      : 22px;
            right    : 28px;
            width    : 17px;
            height   : 17px;
            font-size: 17px;
            color    : #535353;

            &:hover {
                cursor: pointer;
            }
        }

        .modal_img {
            width    : 324px;
            height   : 324px;
            position : absolute;
            top      : -89px;
            left     : 50%;
            transform: translate(-50%, 0);
        }

        .main_board {
            width           : 100%;
            height          : 285px;
            border          : none;
            border-radius   : 30px;
            background-color: #FDF2D0;
            padding         : 0 20px;
            padding-top     : 107px;

            .title {
                width         : 100%;
                font-family   : Poppins;
                font-size     : 40px;
                font-style    : normal;
                font-weight   : 700;
                line-height   : 50px;
                letter-spacing: 0.02em;
                text-align    : center;
                color         : var(--red);
            }

            .content {
                width         : 100%;
                margin-top    : 20px;
                font-family   : Poppins;
                font-size     : 20px;
                font-style    : normal;
                font-weight   : 500;
                line-height   : 26px;
                letter-spacing: 0.02em;
                text-align    : center;
            }
        }

        .text {
            width         : 100%;
            font-family   : Poppins;
            font-size     : 14px;
            font-style    : normal;
            font-weight   : 400;
            line-height   : 21px;
            letter-spacing: 0.02em;
            text-align    : center;
            color         : #000;
            margin-top    : 24px;
        }

        .social_icons {
            width          : 100%;
            margin-top     : 12px;
            display        : flex;
            justify-content: center;

            .social_icon {
                width           : 32px;
                height          : 32px;
                border          : none;
                border-radius   : 10px;
                background-color: var(--red);
                display         : flex;
                justify-content : center;
                align-items     : center;
                font-size       : 25px;
                color           : #FFF;
                margin          : 0 6px;

                &>img {
                    width : 20px;
                    height: 18px;
                }
            }
        }
    }
}