.user_card {
    width              : 231px;
    height          : 232px;
    border-radius      : 20px;
    box-shadow         : 0px 4px 18px -5px #00000040;
    background-color   : #FFF;
    position           : relative;
    box-sizing         : content-box;
    border             : 1px solid transparent;

    .header_img_container {
        width   : 100%;
        height  : 111px;
        position: relative;

        &>img {
            width                  : 100%;
            height                 : 100%;
            border                 : none;
            border-top-left-radius : 20px;
            border-top-right-radius: 20px;
        }

        &>a img {
            width        : 100px;
            height       : 105px;
            border       : 5px solid #FFFFFF;
            border-radius: 50%;
            position     : absolute;
            left         : 50%;
            bottom       : 0;
            transform    : translate(-50%, 33%);
        }
    }

    .content {
        width  : 100%;
        padding: 40px 10px 8px 15px;

        .title {
            width         : 100%;
            font-family   : Poppins;
            font-size     : 14px;
            font-style    : normal;
            font-weight   : 700;
            line-height   : 21px;
            letter-spacing: 0em;
            text-align    : left;
            color         : var(--red);
        }

        .subtitle {
            font-family   : Poppins;
            font-size     : 12px;
            font-style    : normal;
            font-weight   : 400;
            line-height   : 17px;
            letter-spacing: 0em;
            text-align    : left;
            color         : #B4B4B4;
        }

        .action {
            width          : 100%;
            margin-top     : 7px;
            display        : flex;
            justify-content: space-between;
            align-items    : center;

            .info {
                font-family   : Poppins;
                font-size     : 9px;
                font-style    : normal;
                font-weight   : 400;
                line-height   : 12px;
                letter-spacing: 0em;
                text-align    : left;
                color         : #B4B4B4;
                display       : flex;
                align-items   : center;

                .info_icon {
                    width: 16px;
                    height: 11px;
                    margin-right: 4px;
                }
            }

            .btn_container {
                // width: 100%;
                user-select: none;

                .action_btn {
                    width           : 91px;
                    height          : 32px;
                    border          : none;
                    border-radius   : 10px;
                    background-color: var(--red);
                    display         : flex;
                    justify-content : center;
                    align-items     : center;
                    font-family     : Poppins;
                    font-size       : 12px;
                    font-style      : normal;
                    font-weight     : 300;
                    line-height     : 18px;
                    letter-spacing  : 0em;
                    text-align      : left;
                    color           : #FFF;

                    &:hover {
                        cursor: pointer;
                    }
                }

                .action_btn.active {
                    width           : 91px;
                    height          : 32px;
                    border          : none;
                    border-radius   : 10px;
                    background-color: #F5C242;
                    display         : flex;
                    justify-content : center;
                    align-items     : center;
                    font-family     : Poppins;
                    font-size       : 12px;
                    font-style      : normal;
                    font-weight     : 300;
                    line-height     : 18px;
                    letter-spacing  : 0em;
                    text-align      : left;
                    color           : #FFF;
                }
            }
        }
    }
}

.user_card:hover {
    // border: 1px solid #E53222;
    border-color: #E53222;
}