.university_content {
  width                        : 100%;
  border                       : none;
  border-radius                : 30px;
  border-bottom-left-radius    : 0;
  border-bottom-right-radius   : 0;
  background-color             : #FFF;
  position                     : relative;
  // height                    : 200px;

  .tab_btn_container {
    position : absolute;
    top      : 0;
    left     : 50%;
    transform: translate(-50%, -100%);

    .tab_btn {
      display        : flex;
      justify-content: center;

      .tab_first_btn {
        height                : 27px;
        width                 : 187px;
        border-top-left-radius: 50px;
        border                : none;
      }


      .tab_second_btn {
        height                 : 27px;
        width                  : 187px;
        border-top-right-radius: 50px;
        border                 : none;
      }

      .tab_first_btn:hover,
      .tab_second_btn:hover {
        cursor: pointer;
      }

      .w_back {
        background-color: #FFF;
        color           : #E53222;
      }

      .r_back {
        background-color: #EE8885;
        color           : #FFF;
      }
    }
  }

  .university_content_content {
    width         : 100%;
    padding-top   : 30px;
    padding-right : 30px;
    padding-left  : 30px;
    padding-bottom: 38px;

    .content_header {
      width: 100%;

      .title {
        font-family   : Poppins;
        font-size     : 24px;
        font-style    : normal;
        font-weight   : 700;
        line-height   : 36px;
        letter-spacing: 0em;
        text-align    : left;
        color         : var(--red);
      }

      .subtitle {
        margin-top    : 2px;
        font-family   : Poppins;
        font-size     : 18px;
        font-style    : normal;
        font-weight   : 400;
        line-height   : 27px;
        letter-spacing: 0em;
        text-align    : left;
        color         : var(--grey);

        &>a {
          color: var(--grey);
        }
      }
    }

    .video {
      width     : 100%;
      margin-top: 48px;

      .video_header {
        width: 100%;

        .title {
          font-family   : Poppins;
          font-size     : 30px;
          font-style    : normal;
          font-weight   : 700;
          line-height   : 34px;
          letter-spacing: 0.02em;
          text-align    : left;
          color         : var(--red);
        }

        .subtitle {
          font-family   : Poppins;
          font-size     : 20px;
          font-style    : normal;
          font-weight   : 500;
          line-height   : 26px;
          letter-spacing: 0.02em;
          text-align    : left;
          color         : var(--grey);
        }
      }

      .video_container {
        width     : 100%;
        margin-top: 20px;
      }
    }

    .progress {
      width     : 100%;
      margin-top: 32px;


      .progress_container {
        width          : 100%;
        display        : flex;
        justify-content: center;

        .container {
          width: 90%;
        }
      }

      .text {
        margin-top    : 10px;
        font-family   : Poppins;
        font-size     : 14px;
        font-style    : normal;
        font-weight   : 400;
        line-height   : 21px;
        letter-spacing: 0.02em;
        text-align    : center;
        color         : var(--grey);
      }
    }

    .course {
      width     : 100%;
      margin-top: 13px;

      .title {
        font-family   : Poppins;
        font-size     : 20px;
        font-style    : normal;
        font-weight   : 500;
        line-height   : 26px;
        letter-spacing: 0.02em;
        text-align    : left;
        color         : var(--grey);
        margin-bottom : 21px;
      }

      .course_container {}
    }

    .relate {
      width     : 100%;
      margin-top: 176px;

      .title {
        font-family   : Poppins;
        font-size     : 20px;
        font-style    : normal;
        font-weight   : 500;
        line-height   : 26px;
        letter-spacing: 0.02em;
        text-align    : left;
        color         : var(--grey);
      }

      .relate_container {
        width     : 100%;
        margin-top: 20px;
      }

    }

  }

}