.notification {
    width           : 430px;
    height          : 362px;
    border          : none;
    border-radius   : 26px;
    background-color: #F5C242;
    box-shadow      : 4px 7px 8px 0px #00000040;
    position        : absolute;
    right           : 0;
    bottom          : 60px;
    transform       : translate(calc(100% - 50px), 0);
    z-index         : 20;
    pointer-events  : all;

    .notification_container {
        width   : 100%;
        height  : 100%;
        position: relative;
        padding : 0 24px;

        .header {
            width        : 100%;
            height       : 60px;
            border-bottom: 1px solid #FFFFFF;
            display      : flex;
            padding-left : 23px;

            .tab_btn {
                height        : 100%;
                padding       : 34px 7px 7px 5px;
                font-family   : Poppins;
                font-size     : 12px;
                font-style    : normal;
                font-weight   : 600;
                line-height   : 18px;
                letter-spacing: 0em;
                text-align    : left;
                color         : #FFF;
                margin-right  : 40px;
                position      : relative;
                user-select   : none;

                .footer_bar {
                    width           : 100%;
                    height          : 3px;
                    border          : none;
                    border-radius   : 3px;
                    position        : absolute;
                    bottom          : 0;
                    left            : 0;
                    background-color: #B02418;
                    transform       : translateY(50%);
                }

                .tab_count {
                    height          : 11px;
                    padding         : 0 7px;
                    border-radius   : 16px;
                    border          : none;
                    font-family     : Poppins;
                    font-size       : 10px;
                    font-style      : normal;
                    font-weight     : 600;
                    line-height     : 11px;
                    letter-spacing  : 0em;
                    text-align      : center;
                    background-color: #E53222;
                    color           : #FFF;
                    position        : absolute;
                    bottom          : 10px;
                    right           : 0;
                    transform       : translateX(100%);
                }

                &:hover {
                    cursor: pointer;
                }
            }
        }

        .content {
            width: 100%;

            .mark_read {
                width          : 100%;
                height         : 24px;
                display        : flex;
                justify-content: flex-end;
                padding        : 6px 0;

                .mark_read_btn {
                    font-family     : Poppins;
                    font-size       : 8px;
                    font-style      : normal;
                    font-weight     : 600;
                    line-height     : 12px;
                    letter-spacing  : 0em;
                    text-align      : left;
                    color           : #FFF;
                    background-color: transparent;
                    user-select     : none;

                    &:hover {
                        cursor: pointer;
                    }
                }
            }

            .list_container {
                width     : 100%;
                height    : 278px;
                overflow-y: auto;


                &::-webkit-scrollbar {
                    width: 8px;
                }

                &::-webkit-scrollbar-track {
                    background-color: unset;
                    border-radius   : 100px;
                }

                &::-webkit-scrollbar-thumb {
                    background-color: transparent;
                    border          : 3px solid rgba(0, 0, 0, 0);
                    background-clip : padding-box;
                    border-radius   : 6px;
                }

                &:hover::-webkit-scrollbar-thumb {
                    background-color: rgba(177, 34, 34, 1);
                }
            }
        }

        .close_btn {
            position : absolute;
            top      : 15px;
            right    : 25px;
            font-size: 12px;
            color    : #FFF;

            &:hover {
                cursor: pointer;
            }
        }
    }
}