.menuitem {
    width          : 100%;
    position       : relative;
    margin-bottom  : 10px;
    display        : flex;
    justify-content: center;

    .hover_btn,
    .nohover_btn {
        width: 74.78260869565217%;

        .left_btn {
            width           : 28px;
            height          : 40px;
            position        : absolute;
            left            : 0;
            top             : 0;
            transform       : translate(-50%, 0);
            background-color: transparent;
            border          : none;
            border-radius   : 13px;
        }

        .left_btn.active {
            background: #F5C242;
        }

        .menuitem_btn {
            height          : 40px;
            width           : 100%;
            padding         : 0;
            color           : #fff;
            background-color: transparent;
            border          : none;
            font-family     : Poppins;
            font-size       : 14px;
            font-style      : normal;
            font-weight     : 600;
            line-height     : 21px;
            letter-spacing  : 0em;
            text-align      : left;
            display         : flex;
            align-items     : center;
            border-radius   : 13px;

            .menuitem_icon {
                width       : 13.37209302325581%;
                margin-left : 10px;
                margin-right: 21px;
                height      : 52.5%;
            }

            .badge {
                margin-left : 10px;
                margin-right: 21px;

                span {
                    height          : 13px;
                    padding         : 0 2.5px;
                    min-width       : 13px;
                    font-size       : 9px;
                    background-color: #F5C242;
                    top             : 7px;
                    right           : 4px;
                }
            }

        }

        .menuitem_btn.active {
            background-color: #F5C242;

            // .badge {
            //     margin-left : 10px;
            //     margin-right: 21px;

            //     span {
            //         height          : 13px;
            //         padding         : 0 2.5px;
            //         min-width       : 13px;
            //         font-size       : 9px;
            //         background-color: #F5C242;
            //     }
            // }
        }
    }

    .hover_btn:hover .menuitem_btn,
    .hover_btn:hover .left_btn {
        cursor: pointer;
    }

    .nohover_btn:hover .menuitem_btn,
    .nohover_btn:hover .left_btn {
        background-color: #EE8885;
        cursor          : pointer;
    }


}

.small_menuitem {
    margin-bottom  : 10px;
    display        : flex;
    justify-content: center;

    .small_menuitem_btn.active {
        width           : 44px;
        height          : 40px;
        border          : none;
        border-radius   : 15px;
        background-color: #F5C242;
        display         : flex;
        align-items     : center;
        justify-content : center;
    }

    .small_menuitem_btn {
        width           : 44px;
        height          : 40px;
        border          : none;
        background-color: transparent;
        display         : flex;
        align-items     : center;
        justify-content : center;
        border-radius   : 15px;
        color           : #FFF;

        .small_menuitem_icon {
            width : 24px;
            height: 24px;
        }

        .badge {

            span {
                height          : 13px;
                padding         : 0 2.5px;
                min-width       : 13px;
                font-size       : 9px;
                background-color: #F5C242;
                top             : 7px;
                right           : 4px;
            }
        }
    }

    .small_menuitem_btn:hover {
        cursor: pointer;
    }


    .small_menuitem_btn:not(.active):hover {
        background-color: #EE8885;
    }
}

