.pagination {
    width: 100%;

    .pagination_container {
        width: 100%;

        .item_container {
            width          : 100%;
            display        : flex;
            justify-content: space-between;
            margin-bottom  : 10px;
        }

        .last_item_container {
            width          : 100%;
            display        : flex;
            justify-content: space-between;
            margin-bottom  : 0;
        }

        // .max_page {
        //     justify-content: flex-start !important;

        //     &>a {
        //         margin-right: 10px;
        //     }
        // }

        .control_btn_container {
            width          : 100%;
            margin-top     : 37px;
            padding-right  : 11px;
            display        : flex;
            justify-content: flex-end;
            align-items    : center;

            .display {
                font-family   : Poppins;
                font-size     : 20px;
                font-style    : normal;
                font-weight   : 500;
                line-height   : 26px;
                letter-spacing: 0.02em;
                color         : #535353;
                user-select   : none;
            }

            .control_btn {
                width           : 34px;
                height          : 34px;
                margin-left     : 13px;
                border          : none;
                border-radius   : 50%;
                background-color: var(--red);
                display         : flex;
                justify-content : center;
                align-items     : center;
                color           : #FFF;
            }

            .control_btn:hover {
                cursor: pointer;
            }
        }
    }
}

.empty_marketing_div {
    width           : 226px;
    height          : 225px;
    background-color: transparent;
    user-select     : none;
}

.empty_user_div {
    width           : 232px;
    height          : 1px;
    background-color: transparent;
    user-select     : none;
}