.university_content {
  width                        : 100%;
  border                       : none;
  border-radius                : 30px;
  border-bottom-left-radius    : 0;
  border-bottom-right-radius   : 0;
  background-color             : #FFF;
  position                     : relative;
  // height                    : 200px;

  .tab_btn_container {
    position : absolute;
    top      : 0;
    left     : 50%;
    transform: translate(-50%, -100%);

    .tab_btn {
      display        : flex;
      justify-content: center;

      .tab_first_btn {
        height                : 27px;
        width                 : 187px;
        border-top-left-radius: 50px;
        border                : none;
      }


      .tab_second_btn {
        height                 : 27px;
        width                  : 187px;
        border-top-right-radius: 50px;
        border                 : none;
      }

      .tab_first_btn:hover,
      .tab_second_btn:hover {
        cursor: pointer;
      }

      .w_back {
        background-color: #FFF;
        color           : #E53222;
      }

      .r_back {
        background-color: #EE8885;
        color           : #FFF;
      }
    }
  }

  .university_first_content_container {
    width        : 100%;
    padding-top  : 30px;
    padding-right: 30px;
    padding-left : 30px;
    display      : flex;

    .university_left_header {
      width: 285px;

      .content_menu {
        width: 285px;

        .content_menu_title {
          font-family   : Poppins;
          font-size     : 24px;
          font-style    : normal;
          font-weight   : 700;
          line-height   : 36px;
          letter-spacing: 0em;
          text-align    : left;
          color         : #E53222;
        }

        .content_menu_list {
          margin-top: 48px;

          .menu_item {
            font-family   : Poppins;
            font-size     : 18px;
            font-style    : normal;
            font-weight   : 300;
            line-height   : 21px;
            letter-spacing: 0em;
            text-align    : left;
            color         : #535353;
            margin-bottom : 8px;
          }

          .menu_item:last-child {
            margin-bottom: 107px;
          }

          .menu_item:hover {
            cursor: pointer;
          }

          .menu_item.active {
            font-family   : Poppins;
            font-size     : 18px;
            font-style    : normal;
            font-weight   : 700;
            line-height   : 27px;
            letter-spacing: 0em;
            text-align    : left;
            color         : #E53222;
          }
        }
      }

      .category_menu {
        width     : 200px;
        border    : none;
        border-top: 1px solid #EDEDED;

        .category_menu_title {
          margin-top    : 15px;
          font-family   : Poppins;
          font-size     : 18px;
          font-style    : normal;
          font-weight   : 600;
          line-height   : 21px;
          letter-spacing: 0em;
          text-align    : left;
          color         : #E53222;
        }

        .category_menu_list {
          margin-top  : 25px;
          padding-left: 2px;

          .menu_item {
            font-family   : Poppins;
            font-size     : 18px;
            font-style    : normal;
            font-weight   : 300;
            line-height   : 21px;
            letter-spacing: 0em;
            text-align    : left;
            color         : #535353;
            border        : none;
            margin-bottom : 8px;
          }

          .menu_item:hover {
            cursor: pointer;
          }

          .menu_item.active {
            font-family   : Poppins;
            font-size     : 18px;
            font-style    : normal;
            font-weight   : 300;
            line-height   : 21px;
            letter-spacing: 0em;
            text-align    : left;
            color         : #535353;
            border        : none;
            margin-bottom : 8px;
            position      : relative;

            .active_bar {
              position        : absolute;
              width           : 4px;
              height          : 32px;
              border-radius   : 10px;
              border          : none;
              background-color: #E53222;
              top             : -4px;
              left            : -9px;
            }
          }
        }
      }
    }

    .university_first_content {
      width        : calc(100% - 285px);
      margin-bottom: 90px;

      .no_result {
        position   : absolute;
        left       : 50%;
        top        : 40px;
        transform  : translateX(-50%);
        font-size  : 30px;
        color      : var(--red);
        font-weight: 600;
      }

      .university_search {
        width          : 100%;
        margin-top     : 4px;
        display        : flex;
        justify-content: flex-end;

        .search_input_container {
          width           : 480px;
          height          : 36px;
          border          : 1px solid #B4B4B4;
          border-radius   : 10px;
          padding         : 7px 21px 7px 34px;
          background-color: transparent;
          display         : flex;
          align-items     : center;

          .search_input {
            width           : 100%;
            height          : 100%;
            border          : none;
            background-color: transparent;
            color           : #535353;
            outline         : none;
          }

          .input_icon {
            width : 19px;
            height: 19px;

            .search_icon {
              width : 100%;
              height: 100%;
            }
          }
        }
      }

      .university_video {
        width     : 100%;
        margin-top: 43px;
      }

      .marketing {
        width     : 100%;
        margin-top: 84px;

        .marketing_title {
          font-family   : Poppins;
          font-size     : 18px;
          font-style    : normal;
          font-weight   : 300;
          line-height   : 27px;
          letter-spacing: 0em;
          text-align    : left;
          color         : #535353;
        }

        .marketing_content {
          width     : 100%;
          margin-top: 30px;
        }
      }

      .e_marketing {
        width     : 100%;
        margin-top: 5px;

        .e_marketing_title {
          font-family   : Poppins;
          font-size     : 18px;
          font-style    : normal;
          font-weight   : 300;
          line-height   : 27px;
          letter-spacing: 0em;
          text-align    : left;
          color         : #535353;
        }

        .e_marketing_content {
          width     : 100%;
          margin-top: 30px;
        }
      }

      .forum {
        width     : 100%;
        margin-top: 12px;

        .forum_title {
          font-family   : Poppins;
          font-size     : 20px;
          font-style    : normal;
          font-weight   : 500;
          line-height   : 26px;
          letter-spacing: 0.02em;
          text-align    : left;
          margin-bottom : 20px;
        }

        .forum_container {
          .more_view_btn {
            width                     : 100%;
            height                    : 38px;
            background-color          : #FFF;
            border                    : 1px solid #E53222;
            border-radius             : 10px;
            font-family               : Poppins;
            font-size                 : 14px;
            font-style                : normal;
            font-weight               : 400;
            line-height               : 21px;
            letter-spacing            : 0.02em;
            text-align                : center;
            color                     : var(--red);
            transition                : color 0.2s, background-color 0.2s;
            transition-timing-function: ease;
          }

          .more_view_btn:hover {
            cursor          : pointer;
            background-color: var(--red);
            color           : #FFF;
          }
        }
      }
    }
  }



  .university_second_content_container {
    background-color: #FFF;
    width           : 100%;
    height          : 1000px;
  }
}