.course {
    width      : 100%;
    padding-top: 25px;
    position   : relative;

    .course_item {
        width          : 100%;
        padding        : 0 42px;
        display        : flex;
        justify-content: flex-start;
        align-items    : center;
        position       : relative;
        margin-bottom  : 25px;

        .first {
            width  : 100%;
            display: flex;

            .check {
                width : 22px;
                height: 22px;

                input[type='checkbox'].course_check,
                input[type='radio'] {

                    --active          : #F5C242;
                    --active-inner    : #fff;
                    // --focus: 2px rgba(39, 94, 254, .3);
                    --border          : #EB534C;
                    --border-hover    : #EB534C;
                    --background      : #EB534C;
                    --disabled        : #F6F8FF;
                    --disabled-inner  : #E1E6F9;
                    -webkit-appearance: none;
                    -moz-appearance   : none;
                    height            : 22px;
                    outline           : none;
                    display           : inline-block;
                    vertical-align    : top;
                    position          : relative;
                    margin            : 0;
                    cursor            : pointer;
                    border            : 1px solid var(--bc, var(--border));
                    background        : var(--b, var(--background));
                    transition        : background .3s, border-color .3s, box-shadow .2s;
                    margin-right      : 5px;

                    &:after {
                        content   : '';
                        display   : block;
                        left      : 0;
                        top       : 0;
                        position  : absolute;
                        transition: transform var(--d-t, .3s) var(--d-t-e, ease), opacity var(--d-o, .2s);
                    }

                    &:checked {
                        --b    : var(--active);
                        --bc   : var(--active);
                        --d-o  : .3s;
                        --d-t  : .6s;
                        --d-t-e: cubic-bezier(.2, .85, .32, 1.2);
                    }

                    &:disabled {
                        --b    : var(--disabled);
                        cursor : not-allowed;
                        opacity: .9;

                        &:checked {
                            --b : var(--disabled-inner);
                            --bc: var(--border);
                        }

                        &+label {
                            cursor: not-allowed;
                        }
                    }

                    &:hover {
                        &:not(:checked) {
                            &:not(:disabled) {
                                --bc: var(--border-hover);
                            }
                        }
                    }

                    &:focus {
                        box-shadow: 0 0 0 var(--focus);
                    }

                    &:not(.switch) {
                        width: 22px;

                        &:after {
                            opacity: var(--o, 0);
                        }

                        &:checked {
                            --o: 1;
                        }
                    }

                    &+label {
                        font-size     : 14px;
                        line-height   : 21px;
                        display       : inline-block;
                        vertical-align: top;
                        cursor        : pointer;
                        margin-left   : 4px;
                    }
                }

                input[type='checkbox'] {
                    &:not(.switch) {
                        border-radius: 7px;

                        &:after {
                            width      : 5px;
                            height     : 12px;
                            border     : 2px solid var(--active-inner);
                            border-top : 0;
                            border-left: 0;
                            left       : 7px;
                            top        : 2px;
                            transform  : rotate(var(--r, 20deg));
                        }

                        &:checked {
                            --r: 43deg;
                        }
                    }
                }
            }

            .title {
                margin-left   : 35px;
                font-family   : Poppins;
                font-size     : 14px;
                font-style    : normal;
                font-weight   : 400;
                line-height   : 21px;
                letter-spacing: 0.02em;
                text-align    : left;
                color         : var(--grey);

                &:hover {
                    cursor: pointer;
                }
            }
        }

        .last {
            position      : absolute;
            right         : 42px;
            top           : 50%;
            transform     : translate(0, -50%);
            font-family   : Poppins;
            font-size     : 14px;
            font-style    : normal;
            font-weight   : 400;
            line-height   : 21px;
            letter-spacing: 0.02em;
            color         : var(--grey);
        }

        // &:last-child {
        //     margin-bottom: 3px;
        // }
    }

    .action_btn_container {
        width        : 100%;
        height       : 40px;
        padding-right: 42px;

        margin-bottom: 10px;
        overflow-y   : visible;

        .rel_container {
            width          : 100%;
            display        : flex;
            justify-content: flex-end;

            .action_btn {
                width           : 167px;
                height          : 40px;
                border-radius   : 8px;
                border          : 1px solid #F5C242;
                background-color: #FFF;
                font-family     : Poppins;
                font-size       : 14px;
                font-style      : normal;
                font-weight     : 400;
                line-height     : 21px;
                letter-spacing  : 0.02em;
                color           : var(--grey);
                display         : flex;
                align-items     : center;
                justify-content : center;
                position        : relative;
                user-select     : none;

                .folder_icon {
                    position: absolute;
                    left    : 15px;
                    color   : #F5C242;
                }

                .up_icon {
                    position: absolute;
                    right   : 15px;
                    color   : var(--grey);
                }

                &:hover {
                    cursor: pointer;
                }
            }

            .action {
                width           : 196px;
                height          : 40px;
                margin-top      : 5px;
                border          : 1px solid #F5C242;
                border-radius   : 8px;
                background-color: #FFF;
                box-shadow      : 0px 4px 4px 0px #00000040;
                z-index         : 5;
                position        : relative;
                display         : flex;
                justify-content : flex-start;
                align-items     : center;
                font-family     : Poppins;
                font-size       : 12px;
                font-style      : normal;
                font-weight     : 400;
                line-height     : 17px;
                letter-spacing  : 0em;
                color           : var(--grey);
                user-select     : none;

                .download_icon {
                    margin-left : 20px;
                    margin-right: 9px;
                    color       : #F5C242;
                    font-size   : 16px;
                }

                &:hover {
                    cursor: pointer;
                }
            }
        }





    }
}