.main {
  overflow            : hidden;
  position            : relative;
  width               : 100%;
  height              : 100%;
  // background-image : url('../../assets/image/back/home_back.png');
  // background-repeat: no-repeat;
  // background-size  : 100% 100%;
  pointer-events      : all;

  .profile {
    width           : 595px;
    height          : 76%;
    position        : absolute;
    left            : 50%;
    top             : 23%;
    transform       : translate(-50%, 0);
    background-color: transparent;

    .profile_container {
      width   : 100%;
      height  : 100%;
      position: relative;
      overflow: auto;



      .user_description_card {
        width                    : 100%;
        height                   : 220px;
        // margin-top            : 163px;
        // margin-top         : 26.8976897689769%;
        border                   : none;
        border-radius            : 30px;
        background-color         : #FFF;
        position                 : relative;
        padding                  : 70px 75px 50px 75px;



        .name {
          width         : 100%;
          font-family   : Poppins;
          font-size     : 20px;
          font-style    : normal;
          font-weight   : 500;
          line-height   : 30px;
          letter-spacing: 0em;
          text-align    : center;
          color         : #B02418;
        }

        .id {
          margin-top    : 4px;
          font-family   : Poppins;
          font-size     : 16px;
          font-style    : normal;
          font-weight   : 500;
          line-height   : 24px;
          letter-spacing: 0em;
          text-align    : center;
          color         : #EBEBEB;
        }

        .content {
          width         : 100%;
          margin-top    : 16px;
          font-family   : Poppins;
          font-size     : 18px;
          font-style    : normal;
          font-weight   : 300;
          line-height   : 21px;
          letter-spacing: 0em;
          text-align    : center;
          color         : var(--red);
        }

        .edit_btn {
          width   : 24px;
          height  : 24px;
          position: absolute;
          bottom  : 12px;
          right   : 17px;

          * {
            color: #F5C242;
          }
        }
      }

      .user_info_card {
        width           : 100%;
        height          : 323px;
        margin-top      : 8px;
        border          : none;
        border-radius   : 30px;
        background-color: #FFF;
        position        : relative;
        padding-top     : 20px;
        padding-left    : 35px;
        display         : flex;

        .left_info {
          width: 50%;

          .title {
            width         : 100%;
            font-family   : Poppins;
            font-size     : 18px;
            font-style    : normal;
            font-weight   : 600;
            // line-height   : 28px;
            letter-spacing: 0em;
            text-align    : left;
            color         : #B02418;
            margin-bottom: 9px;
          }

          .content {
            width         : 100%;
            font-family   : Poppins;
            font-size     : 18px;
            font-style    : normal;
            font-weight   : 400;
            // line-height   : 48px;
            letter-spacing: 0em;
            text-align    : left;
            color         : var(--grey);
            margin-bottom: 14px;
          }
        }

        .right_info {
          width: 50%;



          .header {
            font-family    : Poppins;
            font-size      : 18px;
            font-style     : normal;
            font-weight    : 600;
            line-height    : 48px;
            letter-spacing : 0em;
            text-align     : left;
            color          : #B02418;
            display        : flex;
            justify-content: center;
          }

          .social_icons {
            margin-top     : 12px;
            width          : 100%;
            display        : flex;
            justify-content: center;

            .icon {
              width           : 32px;
              height          : 32px;
              border          : none;
              margin-right    : 12px;

              &>a>img {
                width: 100%;
                height: 100%;
                border: none;
                border-radius: 10px;
              }
            }
          }
        }

        .edit_btn {
          width   : 24px;
          height  : 24px;
          position: absolute;
          bottom  : 12px;
          right   : 17px;

          * {
            color: #F5C242;
          }
        }
      }

      .user_chart_card {
        width: 100%;
        height: 323px;
        border: none;
        border-radius: 30px;
        padding-top: 20px;
        padding-left: 35px;
        display: flex;
        position: relative;
        background-color: #FFF;
        margin-top: 8px;

        .left_chart {
          .title {
            font-family   : Poppins;
            font-size     : 18px;
            font-style    : normal;
            font-weight   : 600;
            line-height   : 48px;
            letter-spacing: 0em;
            text-align    : left;
            color: #B02418;
          }

          .chart_container {
            margin-left: 7px;
            margin-top: 20px;
            position: relative;

            .percent_text {
              position: absolute;
              left: 50%;
              top: 50%;
              transform: translate(-50%, -50%);

              font-family   : Poppins;
              font-size     : 24px;
              font-style    : normal;
              font-weight   : 700;
              line-height   : 36px;
              letter-spacing: 0em;
              text-align    : left;
              color: var(--red);
            }

          }
          
          .under_text {
            font-family   : Poppins;
            font-size     : 18px;
            font-style    : normal;
            font-weight   : 300;
            line-height   : 21px;
            letter-spacing: 0em;
            text-align    : left;
            color : var(--red);
            margin-left: 25px;
            margin-top: 8px;
          }
        }

        .right_chart {
          width: 100%;
          height: 100%;
          padding-left: 44px;
          padding-right: 20px;
        }
      }

      &::-webkit-scrollbar {
        width: 8px;
      }

      &::-webkit-scrollbar-track {
        background-color: unset;
        border-radius   : 100px;
      }

      &::-webkit-scrollbar-thumb {
        background-color: transparent;
        border          : 3px solid rgba(0, 0, 0, 0);
        background-clip : padding-box;
        border-radius   : 6px;
      }

      &:hover::-webkit-scrollbar-thumb {
        background-color: rgba(177, 34, 34, 1);
      }
    }
  }

  .avatar_container {
    width           : 141px;
    height          : 141px;
    border-radius   : 50%;
    border          : none;
    background-color: #F5C242;
    position        : absolute;
    top             : 23%;
    left            : 50%;
    transform       : translate(-50%, -50%);
    box-shadow      : 0px 4px 17px 0px #00000040;
    z-index         : 10;

    .rel_container {
      width   : 100%;
      height  : 100%;
      position: relative;

      &>img {
        width        : 100%;
        height       : 100%;
        border-radius: 50%;
        border       : none;
      }

      .edit_btn {
        position        : absolute;
        right           : 10px;
        bottom          : 2px;
        width           : 26px;
        height          : 26px;
        background-color: #FFF;
        color           : #F5C242;
        border          : none;
        border-radius   : 50%;
        box-shadow      : 0px 4px 4px 0px #00000040;

        &:hover {
          cursor: pointer;
        }
      }
    }
  }

  .home_btn {
    width           : 69px;
    height          : 66px;
    border          : none;
    border-radius   : 15px;
    background-color: #F5C242;
    display         : flex;
    align-items     : center;
    justify-content : center;
    position        : absolute;
    top             : 47px;
    left            : 34px;

    &>img {
      width : 46px;
      height: 46px;
    }
  }
}

@media (max-width: 1280px) {}