.progress {
    width   : 100%;
    height  : 21px;
    position: relative;
    border  : none;

    .back {
        width        : 100%;
        height       : 100%;
        position     : absolute;
        top          : 0;
        left         : 0;
        z-index      : 1;
        border-radius: 5px;
    }

    .bar100 {
        height       : 100%;
        position     : absolute;
        top          : 0;
        left         : 0;
        z-index      : 2;
        border-radius: 5px;
    }

    .bar {
        height                    : 100%;
        position                  : absolute;
        top                       : 0;
        left                      : 0;
        z-index                   : 2;
        border-radius             : 5px;
        border-top-right-radius   : 0;
        border-bottom-right-radius: 0;
    }

    .text {
        position      : absolute;
        left          : 50%;
        top           : 50%;
        transform     : translate(-50%, -50%);
        font-family   : Poppins;
        font-size     : 14px;
        font-style    : normal;
        font-weight   : 400;
        line-height   : 21px;
        letter-spacing: 0.02em;
        z-index       : 3;
        color         : #FFF;
    }
}