.achievement_item {
    width        : 100%;
    padding-top  : 27px;
    margin-bottom: 16px;

    .item {
        width        : 100%;
        padding-right: 15px;
        display      : flex;
        align-items  : center;

        .icon {
            width       : 130px;
            padding-left: 52px;
            color       : #F5C242;
            font-size   : 12px;
        }

        .text {
            width: 73.6%;

            font-family   : Poppins;
            font-size     : 12px;
            font-style    : normal;
            font-weight   : 400;
            line-height   : 17px;
            letter-spacing: 0em;
            text-align    : left;
            color         : var(--grey);
        }
    }
}