.accordion {
    width        : 100%;
    position     : relative;
    margin-bottom: 11px;
    user-select  : none;

    .main {
        width           : 100%;
        height          : 95px;
        border          : 1px solid #E53222;
        border-radius   : 10px;
        padding         : 0;
        background-color: #FFF;
        display         : flex;
        position        : relative;

        .medal_img_container {
            width   : 148px;
            height  : 100%;
            position: relative;

            .medal_img {
                width    : 148px;
                height   : 60px;
                position : absolute;
                left     : 46px;
                top      : 50%;
                transform: translateY(-50%);

                &>img {
                    width : 48px;
                    height: 60px;
                }
            }
        }

        .text {
            width         : 82%;
            height        : 100%;
            padding-top   : 23px;
            padding-bottom: 18px;

            .title {
                font-family   : Poppins;
                font-size     : 20px;
                font-style    : normal;
                font-weight   : 500;
                line-height   : 26px;
                letter-spacing: 0.02em;
                text-align    : left;
                color         : var(--grey);
                margin-bottom : 10px;
            }

            .description {
                font-family   : Poppins;
                font-size     : 14px;
                font-style    : normal;
                font-weight   : 400;
                line-height   : 21px;
                letter-spacing: 0.02em;
                text-align    : left;
                color         : var(--grey);
            }
        }

        .main_icon {
            position : absolute;
            width    : 20px;
            height   : 20px;
            top      : 50%;
            right    : 50px;
            transform: translate(0, -50%);

            * {
                color: var(--grey);
            }
        }


        &:hover {
            cursor: pointer;
        }
    }

    .content {
        width           : 100%;
        margin-top      : 28px;
        border          : 1px solid #B4B4B4;
        border-radius   : 10px;
        background-color: #FFF;
        padding         : 18px 0;
    }
}