.main {
  overflow            : hidden;
  position            : relative;
  width               : 100%;
  height              : 100%;
  // background-image : url('../../assets/image/back/home_back.png');
  // background-repeat: no-repeat;
  // background-size  : 100% 100%;

  .signup {
    width           : 100%;
    height          : 100%;
    overflow        : none;
    position        : relative;
    background-color: transparent;
    display         : flex;
    justify-content : center;
    align-items     : center;

    .signup_card {
      min-height         : 510px;
      width              : 52%;
      // height          : 76%;
      background-color   : #fff;
      border             : none;
      border-radius      : 30px;
      padding            : 55px 70px 45px 70px;
      pointer-events     : all;

      .card_title {
        font-family   : Poppins;
        font-size     : 29px;
        font-style    : normal;
        font-weight   : 700;
        line-height   : 33px;
        letter-spacing: 0em;
        text-align    : center;
        color         : #000;
        margin        : 0 96px;
      }

      .card_subtitle {
        font-family   : Poppins;
        font-size     : 16px;
        font-style    : normal;
        font-weight   : 400;
        line-height   : 18px;
        letter-spacing: 0em;
        text-align    : center;
        color         : #000;
      }

      .signup_input_container {
        .signup_input_label {
          font-family   : Poppins;
          font-size     : 18px;
          font-style    : normal;
          font-weight   : 400;
          line-height   : 48px;
          letter-spacing: 0em;
          text-align    : left;
          color         : #000;
        }

        .signup_input {
          width        : 100%;
          max-height   : 55px;
          padding      : 5px 26px;
          font-size    : 18px;
          color        : #000;
          line-height  : 48px;
          border       : 1px solid #C4C4C4;
          border-radius: 10px;
          outline      : none;
        }

        .signup_input.error {
          border-color: var(--red);
        }
      }

      .signup_input_container.password {
        margin-top: 30px;
      }

      .signup_input_container.repassword {
        margin-top: 20px;
      }

      .signup_confirm_btn_container {
        width          : 100%;
        margin-top     : 47px;
        display        : flex;
        justify-content: center;

        .signup_confirm_btn {
          width           : 55%;
          padding         : 14px 0;
          background-color: #F5C242;
          max-height      : 55px;
          border-radius   : 10px;
          font-size       : 18px;
          line-height     : 27px;
          font-weight     : 600;
          border          : none;
          color           : #FFF;
        }

        .signup_confirm_btn:hover {
          cursor: pointer;
        }
      }
    }

    .message {
      width           : 664px;
      height          : 440px;
      border-radius   : 30px;
      background-color: #FFF;
      padding         : 70px 0;

      .check_container {
        width          : 100%;
        display        : flex;
        justify-content: center;
        align-items    : center;

        .check_icon {
          width           : 137px;
          height          : 137px;
          border          : 11.42px solid #F5C242;
          border-radius   : 50%;
          background-color: #FFF;
          color           : #F5C242;
          display         : flex;
          justify-content : center;
          align-items     : center;

          &>* {
            font-size: 80px;
          }
        }
      }

      .title_container {
        width          : 100%;
        display        : flex;
        justify-content: center;
        align-items    : center;
        margin-top     : 50px;

        .title {
          width         : 470px;
          font-family   : Poppins;
          font-size     : 29px;
          font-style    : normal;
          font-weight   : 700;
          line-height   : 33px;
          letter-spacing: 0em;
          text-align    : center;
          color         : #000;
        }
      }

      .content_container {
        width          : 100%;
        display        : flex;
        justify-content: center;
        align-items    : center;
        margin-top     : 20px;
        text-align     : center;
        font-family    : Poppins;
        font-size      : 24px;
        font-style     : normal;
        font-weight    : 400;
        line-height    : 27px;
        letter-spacing : 0em;
      }
    }
  }
}

@media (max-width: 1280px) {
  .signup {

    .signup_card {
      width: 60%;

      .card_title {
        margin: 0;
      }

    }
  }
}