.market {
    width              : 226px;
    height             : 225px;
    // border          : 1px solid transparent;
    border-radius      : 15px;
    box-shadow         : 0px 5.832353115081787px 15.55294132232666px -10.692646980285645px #00000026;
    background-color   : #FFF;
    position           : relative;
    box-sizing         : content-box;
    border             : 1px solid transparent;

    .market_img_container {
        width : 100%;
        height: 86px;

        .market_img {
            border-radius             : 15px;
            border-bottom-left-radius : 0;
            border-bottom-right-radius: 0;
            width                     : 100%;
            height                    : 100%;
        }
    }

    .market_content_container {
        width   : 100%;
        padding : 16px;
        position: relative;

        .content_title {
            font-family   : Poppins;
            font-size     : 14px;
            font-style    : normal;
            font-weight   : 700;
            line-height   : 17px;
            letter-spacing: 0em;
            text-align    : left;
            color         : var(--red);
        }

        .content {
            margin-top    : 12px;
            font-family   : Poppins;
            font-size     : 9px;
            font-style    : normal;
            font-weight   : 400;
            line-height   : 12px;
            letter-spacing: 0em;
            text-align    : left;
            color         : #535353;
        }
    }

    .subcontent_container {
        position   : absolute;
        bottom     : 0;
        left       : 0;
        width      : 100%;
        padding    : 16px;
        padding-top: 0;

        .subcontent {
            font-family   : Poppins;
            font-size     : 9px;
            font-style    : normal;
            font-weight   : 400;
            line-height   : 12px;
            letter-spacing: 0em;
            text-align    : left;
            color         : #B4B4B4;

            // .dot {
            //     display            : list-item;
            //     list-style-position: inside;
            //     color              : #B4B4B4;

            //     span {
            //         color   : #B4B4B4;
            //         position: relative;
            //         left    : -6px;
            //     }
            // }
        }
    }
}

.market:hover {
    // border: 1px solid #E53222;
    border-color: #E53222;
}