.chats {
    position   : absolute;
    right      : 50px;
    bottom     : 26px;
    // width   : 100%;
    // height  : 100%;

    .chats_container {
        width          : 100%;
        display        : flex;
        justify-content: flex-end;
        align-items    : flex-end;
        position       : relative;
        height         : 100%;
    }
}

.home_chat_img_container {
    pointer-events : all;
    position       : absolute;
    right          : 0;
    bottom         : 0;
    width          : 52px;
    height         : 52px;
    border         : none;
    border-radius  : 50%;
    background     : #F5C242;
    box-shadow     : 0px 4px 8px 0px #00000040;
    display        : flex;
    align-items    : center;
    justify-content: center;

    .home_chat_img {
        width : 32px;
        height: 32px;
    }
}

.home_chat_img_container:hover {
    cursor: pointer;
}