.sidemenu {
    background-image   : url('../../assets/image/back/sidemenu_back.png');
    background-repeat  : no-repeat;
    background-size    : 100% 100%;
    background-position: top right;
    position           : absolute;
    top                : 0;
    pointer-events     : all;
    overflow-x         : visible;
    width              : 267px;
    padding-right      : 28px;
    z-index            : 100;
    transform          : translateX(-1px);

    transition                : width 0.2s;
    transition-timing-function: ease;

    .sidemenu_small_btn_container {
        width          : 100%;
        display        : flex;
        justify-content: flex-end;
        position       : absolute;
        left           : 0;
        top            : 246px;
        z-index        : 4;


        .sidemenu_small_btn {
            transform       : translate(-18px, 0);
            width           : 20px;
            height          : 34px;
            border          : none;
            border-radius   : 18px;
            background-color: #E53222;
            text-align      : center;
            color           : var(--white);
            display         : flex;
            align-items     : center;
        }

        // .sidemenu_small_btn:hover {
        //     cursor          : pointer;
        //     background-color: #EE8885;
        // }
    }

    .sidemenu_container {
        width     : 100%;
        height    : 100%;
        overflow-x: hidden;
        overflow-y: hidden;
        position  : relative;
        z-index   : 5;


        .sidemenu_header {
            width   : 100%;
            height  : 230px;
            position: relative;

            .sidemenu_header_img_container {
                display           : flex;
                justify-content   : center;
                width             : 100%;
                height            : 100%;
                position          : absolute;
                // top            : -7px;
                top               : 12px;

                .sidemenu_header_img {
                    z-index   : 5;
                    // width  : 158px;
                    // height : 178px;
                    width     : 60px;
                    height    : 135px;
                }
            }

            .sidemenu_header_effect_img_container {
                display        : flex;
                justify-content: center;
                width          : 100%;
                height         : 100%;
                position       : absolute;
                top            : -7px;

                .sidemenu_header_effect_img {
                    z-index: 4;
                }
            }

            .sidemenu_header_card_container {
                display        : flex;
                justify-content: center;
                width          : 100%;
                height         : 102px;
                position       : absolute;
                top            : 115px;

                .sidemenu_header_card {
                    box-shadow            : 0px 4px 29px -12px #00000040;
                    width                 : 171px;
                    height                : 100%;
                    border-radius         : 18px;
                    background-color      : #FFFFFF1A;
                    z-index               : 1;
                    text-align            : center;
                    position              : relative;
                    // border             : 2px solid;
                    // border-image-source: linear-gradient(131.71deg, #F0847A 2.77%, #E84638 46.25%);
                    background            : linear-gradient(131.71deg, #F0847A 2.77%, #E84638 46.25%);
                    padding               : 2px;

                    .sidemenu_header_card_back {
                        background-color: #E84638;
                        width           : 100%;
                        height          : 100%;
                        border-radius   : 18px;

                        .sidemenu_header_card_text {
                            width         : 100%;
                            position      : absolute;
                            left          : 50%;
                            top           : 46.07843137254902%;
                            transform     : translate(-50%, 0);
                            font-size     : 18px;
                            font-style    : normal;
                            font-weight   : 600;
                            line-height   : 27px;
                            letter-spacing: 0em;
                            color         : #FFF;
                        }


                    }
                }
            }

            .sidemenu_header_btn_container {
                width          : 100%;
                display        : flex;
                justify-content: center;
                position       : absolute;
                left           : 0;
                top            : 100%;
                transform      : translate(0, -100%);
                z-index        : 10;

                .sidemenu_header_btn {
                    background-color: #F5C242;
                    width           : 127px;
                    max-height      : 30px;
                    text-align      : center;
                    padding         : 5px 0;
                    font-size       : 14px;
                    font-style      : normal;
                    font-weight     : 600;
                    line-height     : 21px;
                    letter-spacing  : 0em;
                    border          : none;
                    border-radius   : 12px;
                    color           : #FFF;
                }

                .sidemenu_header_btn:hover {
                    cursor: pointer;
                }
            }
        }

        .sidemenu_content {
            width        : 100%;
            margin-top   : 50px;
            margin-bottom: 50px;
            padding-right: 2.54237288135593%;
        }



        &::-webkit-scrollbar {
            // width: 8px;
            width: 0;
        }

        &::-webkit-scrollbar-track {
            background-color: unset;
            border-radius   : 100px;
        }

        &::-webkit-scrollbar-thumb {
            background-color: rgba(255, 255, 255, 0.1);
            border          : 3px solid rgba(0, 0, 0, 0);
            background-clip : padding-box;
            border-radius   : 6px;
        }
    }

    .sidemenu_container:hover {
        overflow-y: auto;
    }
}

.small_sidemenu {
    background-image          : url('../../assets/image/back/small_sidemenu_back.png');
    background-repeat         : no-repeat;
    background-size           : 100% 100%;
    background-position       : top right;
    position                  : absolute;
    top                       : 0;
    pointer-events            : all;
    overflow-x                : visible;
    width                     : 101px;
    padding-right             : 28px;
    z-index                   : 100;
    transform                 : translateX(-1px);
    transition                : width 0.2s;
    transition-timing-function: ease;

    .sidemenu_small_btn_container {
        width          : 100%;
        display        : flex;
        justify-content: flex-end;
        position       : absolute;
        left           : 0;
        top            : 246px;
        z-index        : 4;


        .sidemenu_small_btn {
            transform       : translate(-18px, 0);
            width           : 20px;
            height          : 34px;
            border          : none;
            border-radius   : 18px;
            background-color: #E53222;
            text-align      : left;
            color           : var(--white);
            display         : flex;
            align-items     : center;
            padding         : 0 3px;
        }

        // .sidemenu_small_btn:hover {
        //     cursor          : pointer;
        //     background-color: #EE8885;
        // }
    }

    .sidemenu_container {
        width     : 100%;
        height    : 100%;
        overflow-x: hidden;
        overflow-y: auto;
        position  : relative;
        z-index   : 5;

        .sidemenu_small_header {
            position: relative;
            width   : 100%;
            height  : 144px;

            .sidemenu_small_header_img {
                position   : absolute;
                // top     : 58px;
                top        : 53px;
                left       : 22px;
                // left    : 7px;
                z-index    : 5;
                width      : 34px;
                // width   : 65px;
                height     : 74px;
            }

            .sidemenu_small_header_card {
                position        : absolute;
                top             : 101px;
                left            : 17px;
                width           : 43px;
                height          : 43px;
                border          : none;
                border-radius   : 50%;
                background-color: #F5C242;
                z-index         : 1;
            }
        }

        .sidemenu_small_content {
            margin-top   : 136px;
            width        : 100%;
            padding-left : 12px;
            padding-right: 12px;
        }

        &::-webkit-scrollbar {
            width: 0px;
        }

        &::-webkit-scrollbar-track {
            background-color: unset;
            border-radius   : 100px;
        }

        &::-webkit-scrollbar-thumb {
            background-color: rgba(0, 0, 0, 0.1);
            border          : 3px solid rgba(0, 0, 0, 0);
            background-clip : padding-box;
            border-radius   : 6px;
        }
    }

    // .sidemenu_container:hover {
    //     overflow-y: auto;
    // }
}

@media (max-width: 1200px) {
    .sidemenu {

        .sidemenu_small_btn_container {
            top: 34.1666666667%;
        }

    }

    .small_sidemenu {

        .sidemenu_small_btn_container {
            top: 34.1666666667%;
        }

        .sidemenu_container {
            width     : 100%;
            height    : 100%;
            overflow-x: hidden;
            overflow-y: auto;
            position  : relative;
            z-index   : 5;

            .sidemenu_small_header {
                position: relative;
                width   : 100%;
                height  : 144px;

                .sidemenu_small_header_img {
                    position: absolute;
                    top     : 58px;
                    left    : 7px;
                    z-index : 5;
                    width   : 65px;
                    height  : 74px;
                }

                .sidemenu_small_header_card {
                    position        : absolute;
                    top             : 101px;
                    left            : 17px;
                    width           : 43px;
                    height          : 43px;
                    border          : none;
                    border-radius   : 50%;
                    background-color: #F5C242;
                    z-index         : 1;
                }
            }

            .sidemenu_small_content {
                margin-top   : 36px;
                width        : 100%;
                padding-left : 14px;
                padding-right: 14px;
            }
        }

    }
}

