.member {
    width        : 100%;
    height       : 55px;
    background   : #FFFFFF;
    border       : none;
    border-radius: 18px;
    padding      : 4px 8px;
    margin-bottom: 16px;

    .member_container {
        display    : flex;
        align-items: center;

        .member_avatar {
            width           : 47px;
            height          : 47px;
            border-radius   : 50%;
            border          : none;
            background-color: #FFF;

            .avatar {
                width        : 100%;
                height       : 100%;
                border       : none;
                border-radius: 50%;
            }
        }

        .member_info_container {
            margin-left: 16px;

            .member_name {
                font-family   : Poppins;
                font-size     : 14px;
                font-style    : normal;
                font-weight   : 500;
                line-height   : 21px;
                letter-spacing: 0em;
                text-align    : left;
                color         : #B02418;
            }

            .online_status {
                margin-top    : 5px;
                font-family   : Poppins;
                font-size     : 8px;
                font-style    : normal;
                font-weight   : 500;
                line-height   : 12px;
                letter-spacing: 0em;

                .online {
                    color              : #039A00;
                    text-decoration    : underline;
                    display            : list-item;
                    list-style-position: inside;

                    span {
                        position: relative;
                        color   : #039A00;
                        left    : -6px;
                    }
                }

                .offline {
                    color              : #C4C4C4;
                    text-decoration    : underline;
                    display            : list-item;
                    list-style-position: inside;

                    span {
                        position: relative;
                        color   : #C4C4C4;
                        left    : -6px;
                    }
                }

            }
        }
    }
}

.member {
    cursor: pointer;
}