.main {
  overflow: hidden;
  position: relative;
  width   : 100%;
  height  : 100%;

  .university {
    width     : 100%;
    height    : 100%;
    overflow  : hidden;
    background: transparent;

    .university_container {
      background-image   : url('../../assets/image/back/main_header_back.png');
      background-position: top;
      background-size    : contain;
      background-repeat  : no-repeat;
      overflow-x         : hidden;
      overflow-y         : auto;
      height             : 100%;
      pointer-events     : all;
      position           : absolute;
      top                : 0;
      right              : 0;

      .university_rel_container {
        width   : 100%;
        height  : 100%;
        position: relative;

        .university_header {
          // position        : absolute;
          // top             : 0;
          // left            : 0;
          width           : 100%;
          height          : 265px;
          background-color: #E53222B2;

          .university_header_title {
            width   : 100%;
            position: relative;

            .header_title {
              width          : 100%;
              display        : flex;
              justify-content: center;
              padding-top    : 74px;

              .header_title_img {
                width : 151px;
                height: 100%;
              }
            }

            .header_content {
              font-family   : Poppins;
              font-size     : 20px;
              font-style    : normal;
              font-weight   : 500;
              line-height   : 26px;
              letter-spacing: 0.02em;
              text-align    : center;
              color         : #FFF;
              margin-top    : 30px;
            }

            .header_progress_container {
              position        : absolute;
              top             : 35px;
              right           : 30px;
              width           : 211px;
              height          : 194px;
              border          : none;
              border-radius   : 23px;
              background-color: #FFF;
              padding-top     : 29px;

              .progress_container {
                width          : 100%;
                height         : 102px;
                display        : flex;
                justify-content: center;
                position       : relative;

                .percent_text {
                  position      : absolute;
                  top           : 50%;
                  left          : 50%;
                  transform     : translate(-50%, -50%);
                  font-family   : Poppins;
                  font-size     : 24px;
                  font-style    : normal;
                  font-weight   : 700;
                  line-height   : 36px;
                  letter-spacing: 0em;
                  color         : var(--red);
                }

              }

              .progress_text {
                width         : 100%;
                margin-top    : 16px;
                text-align    : center;
                font-family   : Poppins;
                font-size     : 18px;
                font-style    : normal;
                font-weight   : 300;
                line-height   : 27px;
                letter-spacing: 0em;
                color         : #E53222;
              }
            }
          }
        }

        .red_back {
          width           : 100%;
          // height          : 100%;
          background-color: #E53222B2;

          .university_content {
            width                        : 100%;
            border                       : none;
            border-radius                : 30px;
            border-bottom-left-radius    : 0;
            border-bottom-right-radius   : 0;
            background-color             : #FFF;
            position                     : relative;
            // height                    : 200px;

            .tab_btn_container {
              position : absolute;
              top      : 0;
              left     : 50%;
              transform: translate(-50%, -100%);

              .tab_btn {
                display        : flex;
                justify-content: center;

                .tab_first_btn {
                  height                : 27px;
                  width                 : 187px;
                  border-top-left-radius: 50px;
                  border                : none;
                }


                .tab_second_btn {
                  height                 : 27px;
                  width                  : 187px;
                  border-top-right-radius: 50px;
                  border                 : none;
                }

                .tab_first_btn:hover,
                .tab_second_btn:hover {
                  cursor: pointer;
                }

                .w_back {
                  background-color: #FFF;
                  color           : #E53222;
                }

                .r_back {
                  background-color: #EE8885;
                  color           : #FFF;
                }
              }
            }



            .university_second_content_container {
              background-color: #FFF;
              width           : 100%;
              height          : 1000px;
            }
          }
        }
      }

      &::-webkit-scrollbar {
        width: 0;
      }

      &::-webkit-scrollbar-track {
        // background-color: rgb(245, 81, 17);
        // background-color: transparent;
        border-radius: 100px;
      }

      &::-webkit-scrollbar-thumb {
        border-radius      : 100px;
        background-clip    : content-box;
        // background-color: rgba(255, 255, 255, 0.1);
        background-color   : white;
      }

    }
  }
}

.back_circle {
  position: absolute;
  left    : 0;
}

.back_circle circle {
  color: hsl(45, 92%, 90%) !important;
}

.circle circle {
  color: #E53222 !important;
}

.text {
  color         : #E53222 !important;
  font-family   : Poppins !important;
  font-size     : 24px !important;
  font-style    : normal !important;
  font-weight   : 700 !important;
  line-height   : 36px !important;
  letter-spacing: 0em !important;
  text-align    : center !important;
}