.notification_item {
    width           : 100%;
    border          : none;
    border-radius   : 15px;
    background-color: #FADA78;
    padding         : 11px 18px;
    position        : relative;
    margin-bottom   : 10px;

    .title {
        font-family   : Poppins;
        font-size     : 12px;
        font-style    : normal;
        font-weight   : 600;
        line-height   : 18px;
        letter-spacing: 0em;
        text-align    : left;
        color         : #B02418;
    }

    .content {
        font-family   : Poppins;
        font-size     : 10px;
        font-style    : normal;
        font-weight   : 500;
        line-height   : 15px;
        letter-spacing: 0em;
        text-align    : left;
        color         : #000;
    }

    .btn_container {
        font-family   : Poppins;
        font-size     : 8px;
        font-style    : normal;
        font-weight   : 500;
        line-height   : 12px;
        letter-spacing: 0em;
        text-align    : left;
        color         : #000;
        margin-top    : 7px;
        display       : flex;
        align-items   : center;

        & a {
            color: #000;

            svg {
                margin-left: 3px;
                font-size  : 6px;
                color      : #000;
            }
        }
    }
}

.notification_item.not_read {
    background-color: #FDF2D0;
}