.main {
  overflow        : hidden;
  position        : relative;
  width           : 100%;
  height          : 100%;
  background-color: #FFF;

  .report {
    width           : 100%;
    height          : 100%;
    overflow        : hidden;
    position        : relative;
    background-color: transparent;

    .report_container {
      height        : 100%;
      position      : absolute;
      top           : 0;
      right         : 0;
      pointer-events: all;
      overflow-y    : auto;

      .rel_container {
        width   : 100%;
        position: relative;
        padding : 46px 54px 70px 30px;

        .page_header {
          width      : 100%;
          display    : flex;
          align-items: center;

          .title {
            font-family   : Poppins;
            font-size     : 30px;
            font-style    : normal;
            font-weight   : 700;
            line-height   : 34px;
            letter-spacing: 0.02em;
            text-align    : left;
            color         : var(--grey);
          }

          .export_pdf_btn {
            width           : 100px;
            height          : 25px;
            margin-left     : 65px;
            border-radius   : 14px;
            background-color: var(--red);
            display         : flex;
            justify-content : center;
            align-items     : center;

            font-family   : Poppins;
            font-size     : 12px;
            font-style    : normal;
            font-weight   : 400;
            line-height   : 17px;
            letter-spacing: 0em;
            text-align    : center;
            color         : #FFF;

            user-select: none;

            &:hover {
              cursor: pointer;
            }
          }

        }

        .chart_select {
          width     : 100%;
          margin-top: 8px;

          .select_container {
            width   : 120px;
            position: relative;

            .date_range_select {
              width           : 100%;
              height          : 17px;
              border          : none;
              padding-right   : 10px;
              font-family     : Poppins;
              font-size       : 12px;
              font-style      : normal;
              font-weight     : 400;
              line-height     : 17px;
              letter-spacing  : 0em;
              text-align      : left;
              color           : #B4B4B4;
              background-color: #FFF;
              outline         : none;
              appearance      : none;

              &:hover {
                cursor: pointer;
              }
            }

            .select_icon {
              position : absolute;
              top      : 50%;
              right    : 0;
              transform: translate(0, -50%);
              width    : 16px;
              height   : 16px;

              .arrow_down {
                color    : #B4B4B4;
                font-size: 16px;
              }

            }
          }
        }

        .chart_container {
          width     : 100%;
          margin-top: 14px;

          .title {
            font-family   : Poppins;
            font-size     : 20px;
            font-style    : normal;
            font-weight   : 500;
            line-height   : 26px;
            letter-spacing: 0.02em;
            text-align    : left;
            color         : var(--grey);
          }

          .report_chart {
            width        : 100%;
            height       : 220px;
            border       : 1px solid #E53222;
            border-radius: 9px;
            margin-top   : 8px;
          }
        }

        .certification_container {
          width     : 100%;
          margin-top: 44px;

          .title {
            font-family   : Poppins;
            font-size     : 20px;
            font-style    : normal;
            font-weight   : 500;
            line-height   : 26px;
            letter-spacing: 0.02em;
            text-align    : left;
            color         : var(--grey);
          }

          .certification_content {
            width     : 100%;
            margin-top: 20px;
          }
        }

        .medal_container {
          width     : 100%;
          margin-top: 44px;

          .title {
            font-family   : Poppins;
            font-size     : 20px;
            font-style    : normal;
            font-weight   : 500;
            line-height   : 26px;
            letter-spacing: 0.02em;
            text-align    : left;
            color         : var(--grey);
          }

          .medal_content {
            margin-top: 26px;
            max-width : 522px;
          }
        }

      }


      &::-webkit-scrollbar {
        width: 8px;
      }

      &::-webkit-scrollbar-track {
        background-color: unset;
        border-radius   : 100px;
      }

      &::-webkit-scrollbar-thumb {
        background-color   : transparent;
        // background-color: rgba(177, 34, 34, 1);
        border             : 3px solid rgba(0, 0, 0, 0);
        background-clip    : padding-box;
        border-radius      : 6px;
      }

      &:hover::-webkit-scrollbar-thumb {
        background-color: rgba(177, 34, 34, 1);
      }

    }

  }
}

.export_card {
  position : absolute;
  left     : 50%;
  top      : 150px;
  transform: translateX(-50%);

  .card_container {
    width           : 358px;
    height          : 235px;
    border          : none;
    border-radius   : 45px;
    box-shadow      : 0px 4px 6px 0px #00000040;
    background-color: #FFF;

    padding : 50px 31px 0 31px;
    position: relative;

    .content {
      font-family   : Poppins;
      font-size     : 20px;
      font-style    : normal;
      font-weight   : 500;
      line-height   : 26px;
      letter-spacing: 0.02em;
      text-align    : left;

      color: #039A00;
    }

    .ok_btn {
      position : absolute;
      left     : 50%;
      bottom   : 43px;
      transform: translateX(-50%);

      width           : 128px;
      height          : 41px;
      border          : none;
      border-radius   : 16px;
      background-color: var(--red);

      display        : flex;
      justify-content: center;
      align-items    : center;

      font-family   : Poppins;
      font-size     : 14px;
      font-style    : normal;
      font-weight   : 400;
      line-height   : 21px;
      letter-spacing: 0.02em;
      text-align    : center;
      color         : #FFF;
      user-select   : none;

      &:hover {
        cursor: pointer;
      }
    }

    .msg_close_btn {
      width           : 36px;
      height          : 36px;
      border          : none;
      border-radius   : 50%;
      background-color: var(--red);

      position : absolute;
      right    : 0;
      top      : 0;
      transform: translateX(calc(100% + 12px));

      color    : #FFF;
      font-size: 18px;

      display        : flex;
      justify-content: center;
      align-items    : center;
      user-select    : none;

      &:hover {
        cursor: pointer;
      }
    }
  }
}