.ranking_item {
    height       : 87px;
    position     : relative;
    display      : flex;
    margin-bottom: 9px;

    .number {
        width         : 49px;
        height        : 100%;
        padding       : 34px 6px 0 6px;
        font-family   : Poppins;
        font-size     : 20px;
        font-style    : normal;
        font-weight   : 500;
        line-height   : 26px;
        letter-spacing: 0.02em;
        text-align    : center;
        color         : #EE8885;
    }

    .card_container {
        width           : 939px;
        height          : 100%;
        border          : 2px solid #EB534C;
        border-radius   : 20px;
        background-color: #EB534C;
        padding-left    : 24px;
        padding-right   : 11px;
        display         : flex;
        align-items     : center;
        color           : #FFF;

        font-family   : Poppins;
        font-size     : 14px;
        font-style    : normal;
        font-weight   : 400;
        line-height   : 21px;
        letter-spacing: 0.02em;
        text-align    : left;

        position: relative;

        .img_container {
            width           : 35px;
            height          : 35px;
            border          : none;
            border-radius   : 50%;
            background-color: #F5C242;
            box-shadow      : 0px 4px 17px 0px #00000040;

            &>img {
                width        : 100%;
                height       : 100%;
                border-radius: 50%;
            }
        }

        .name {
            margin-left: 20px;
            width      : 147px;
        }

        .unit {
            width: 148px;
        }

        .office {
            width: 155px;
        }

        .time {
            width: 117px;
        }

        .achievements {
            display: flex;

            .medal_img {
                margin-right: 8px;
                width       : 22px;
                height      : 28px;

                &>img {
                    width : 100%;
                    height: 100%;
                }

                &:last-child {
                    margin: 0;
                }
            }
        }

        .average {
            width    : 108px;
            position : absolute;
            top      : 50%;
            right    : 10px;
            transform: translateY(-50%);
        }
    }

    .card_container.even {
        background-color: #FFF;
        color           : #EB534C;
    }
}