.schedule_item {
    width                     : 100%;
    height                    : 127px;
    border                    : 1px solid #B4B4B4;
    border-radius             : 24px;
    background-color          : #FFF;
    padding                   : 18px 22px 20px 24px;
    position                  : relative;
    margin-bottom             : 10px;
    transition                : border 0.2s;
    transition-timing-function: ease;

    &:hover {
        cursor: pointer;
        border: 1px solid #E53222;
    }

    .title {
        font-family   : Poppins;
        font-size     : 20px;
        font-style    : normal;
        font-weight   : 500;
        line-height   : 26px;
        letter-spacing: 0.02em;
        text-align    : left;
        color         : var(--red);
    }

    .description {
        font-family   : Poppins;
        font-size     : 14px;
        font-style    : normal;
        font-weight   : 400;
        line-height   : 21px;
        letter-spacing: 0.02em;
        text-align    : left;
        color         : var(--grey);
        margin-top    : 4px;
    }

    .item_footer {
        width          : 100%;
        height         : 25px;
        position       : absolute;
        left           : 0;
        bottom         : 20px;
        padding-left   : 24px;
        padding-right  : 22px;
        display        : flex;
        align-items    : center;
        justify-content: space-between;

        .left {
            display    : flex;
            align-items: center;

            .footer_img {
                width           : 25px;
                height          : 25px;
                border-radius   : 50%;
                border          : none;
                background-color: #E6470D;
                margin-right    : 5px;

                &>img {
                    width        : 100%;
                    height       : 100%;
                    border-radius: 50%;
                }
            }

            .footer_text {
                font-family   : Poppins;
                font-size     : 14px;
                font-style    : normal;
                font-weight   : 400;
                line-height   : 21px;
                letter-spacing: 0em;
                text-align    : left;
                color         : #B4B4B4;
            }
        }

        .right {
            display       : flex;
            align-items   : center;
            font-family   : Poppins;
            font-size     : 14px;
            font-style    : normal;
            font-weight   : 400;
            line-height   : 21px;
            letter-spacing: 0em;
            text-align    : left;
            color         : #B4B4B4;
        }
    }
}

.schedule_modal {
    position        : fixed;
    top             : 0;
    left            : 0;
    display         : flex;
    justify-content : center;
    align-items     : center;
    z-index         : 10;
    pointer-events  : none;
    background-color: #00000040;

    .card {
        width           : 726px;
        height          : 622px;
        border          : none;
        border-radius   : 24px;
        background-color: #FFF;
        padding         : 35px 45px 38px 45px;
        position        : relative;
        pointer-events  : all;

        .title {
            width         : 100%;
            font-family   : Poppins;
            font-size     : 24px;
            font-style    : normal;
            font-weight   : 500;
            line-height   : 32px;
            letter-spacing: 0.02em;
            text-align    : left;
            color         : var(--red);
        }

        .content {
            width         : 100%;
            margin-top    : 10px;
            //styleName: H4;
            font-family   : Poppins;
            font-size     : 14px;
            font-style    : normal;
            font-weight   : 400;
            line-height   : 21px;
            letter-spacing: 0.02em;
            text-align    : left;
            color         : var(--grey);
        }

        .video_container {
            width     : 100%;
            margin-top: 20px;
        }

        .footer {
            width          : 100%;
            margin-top     : 33px;
            display        : flex;
            justify-content: space-between;
            align-items    : center;

            .first {
                height     : 40px;
                display    : flex;
                align-items: center;

                &>img {
                    width           : 40px;
                    height          : 40px;
                    border          : none;
                    border-radius   : 50%;
                    background-color: #E6470D;
                }

                .footer_text {
                    font-family   : Poppins;
                    font-size     : 20px;
                    font-style    : normal;
                    font-weight   : 400;
                    line-height   : 26px;
                    letter-spacing: 0.02em;
                    text-align    : left;
                    color         : var(--grey);
                    margin-left   : 10px;
                }
            }

            .second {
                font-family   : Poppins;
                font-size     : 20px;
                font-style    : normal;
                font-weight   : 400;
                line-height   : 26px;
                letter-spacing: 0.02em;
                text-align    : left;
                color         : var(--grey);
            }
        }

        .close_btn {
            position : absolute;
            top      : 22px;
            right    : 28px;
            width    : 17px;
            height   : 17px;
            font-size: 17px;
            color    : #535353;

            &:hover {
                cursor: pointer;
            }
        }
    }
}