.custom-calendar {
  width: 468px;
  height: 458px;
  border: 1px solid #B4B4B4;
  border-radius: 24px !important;
  background-color: #FFF;
  padding-top: 46px;
  padding-left: 70px;
  padding-right:70px;

 box-shadow: none !important;
}

.custom-calendar .rmdp-top-class {
  width: 100%;
  height: 100%;
  display:block !important;
}

.custom-calendar .rmdp-top-class .rmdp-header {
  height: 90px;
  display: flex;
  align-items: center;
}

.custom-calendar .rmdp-top-class .rmdp-header > div {
  width: 100%;
  height: 100%;
}

.custom-calendar .rmdp-header-values span:first-child {
  width: 100%;
  font-family: Poppins;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 48px;
  letter-spacing: 0em;
  text-align: center;
  color: #E53222;

  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.custom-calendar .rmdp-header-values span:last-child {
  font-family: Poppins;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 36px;
  letter-spacing: 0em;
  text-align: center;
  color: #F5C242;

  position: absolute;
  bottom: 5px;
  left: 50%;
  transform: translateX(-50%);
}

.custom-calendar .rmdp-header .rmdp-arrow-container .rmdp-arrow {
  border: solid #000;
  border-width: 0 3px 3px 0;
  width: 12px;
  height: 12px;
}

.custom-calendar .rmdp-calendar .rmdp-day-picker {
  margin-top: 30px;
}

.custom-calendar .rmdp-day-picker {
  display:block;
}

.custom-calendar .rmdp-day-picker>div {
  width:100%;
}

 .custom-calendar .rmdp-week {
  width: 100%;
}

.custom-calendar .rmdp-calendar .rmdp-day-picker .rmdp-day {
  width:42px;
  height: 42px;

  font-family: Poppins;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: center;
  color: #535353;
  user-select:none;
}

.custom-calendar .rmdp-calendar .rmdp-day-picker .rmdp-day.rmdp-deactive {
  color: #EBEBEB;
}

.custom-calendar.red .rmdp-day:not(.rmdp-day-hidden) span:hover {
  background-color: #B4B4B4 !important;
}

.custom-calendar.red .rmdp-day.rmdp-selected span:not(.highlight) {
  background-color: #E53222 !important;

}

.custom-calendar.red .rmdp-arrow-container:hover {
  background-color: transparent;
  box-shadow:none;
}
