.main {
  overflow            : hidden;
  position            : relative;
  width               : 100%;
  height              : 100%;
  // background-image : url('../../assets/image/back/home_back.png');
  // background-repeat: no-repeat;
  // background-size  : 100% 100%;

  .home {
    width           : 100%;
    height          : 100%;
    overflow        : hidden;
    position        : relative;
    background-color: transparent;

    .home_container {
      height  : 100%;
      position: absolute;
      top     : 0;
      right   : 0;

      .home_card_container {
        width          : 100%;
        height         : 100%;
        position       : relative;
        display        : flex;
        justify-content: center;
        padding        : 0 119px 0 55px;
        align-items    : flex-end;

        .home_chat_img_container {
          position       : absolute;
          right          : 50px;
          bottom         : 26px;
          width          : 52px;
          height         : 52px;
          border         : none;
          border-radius  : 50%;
          background     : #F5C242;
          box-shadow     : 0px 4px 8px 0px #00000040;
          display        : flex;
          align-items    : center;
          justify-content: center;

          .home_chat_img {
            width : 32px;
            height: 32px;
          }
        }

        .home_card {
          pointer-events     : all;
          height             : 286px;
          width              : 840px;
          bottom             : 70px;
          background   : #E53222;
          border-radius      : 30px;
          box-shadow         : 0px 4px 35px rgba(0, 0, 0, 0.25);
          //backdrop-filter    : blur(10px);

          // border-image-source: linear-gradient(101.92deg, rgba(239, 175, 90, 0.7) 0%, rgba(239, 175, 90, 0) 37.43%, rgba(239, 175, 90, 0) 60.5%, rgba(239, 175, 90, 0.7) 98.43%);
          padding : 20px 40px 0 73px;
          position      : relative;

          background-clip: padding-box;
          border         : solid 3px transparent;
          color : #FFF;


          // pointer-events : all;
          // display        : flex;
          // align-items    : center;
          // width          : 840px;
          // height : 286px;
          // margin         : auto;
          // position       : relative;
          // padding        : 20px 40px 0 73px;
          // box-sizing     : border-box;
          // 
          // background     : #E53222;
          // background-clip: padding-box;
          // border         : solid 5px transparent;
          // border-radius  : 30px;

          &:before {
            content      : '';
            position     : absolute;
            top          : 0;
            right        : 0;
            bottom       : 0;
            left         : 0;
            z-index      : -1;
            margin       : -3px;
            /* !important */
            border-radius: inherit;
            /* !important */
            background   : linear-gradient(101.92deg, rgba(239, 175, 90, 0.7) 0%, rgba(239, 175, 90, 0) 37.43%, rgba(239, 175, 90, 0) 60.5%, rgba(239, 175, 90, 0.7) 98.43%);
          }

          .home_card_img_container {
            position: absolute;
            left    : -28px;
            bottom  : 11px;
            width: 165px;
            height: 555px;

            .home_card_img {
              width: 100%;
              height: 100%;  
            }
          }

          .home_card_title {
            text-align    : center;
            font-size     : 40px;
            font-style    : normal;
            font-weight   : 700;
            line-height   : 60px;
            letter-spacing: 0.02em;
            color         : #FFF;
          }

          .home_card_content_container {
            display     : flex;
            margin-top  : 14px;
            padding-left: 10%;

            .first_content {
              width      : 50%;
              padding-top: 17px;

              .home_card_content_title {
                font-size     : 24px;
                font-style    : normal;
                font-weight   : 300;
                line-height   : 27px;
                letter-spacing: 0em;
                text-align    : center;
                color         : #FFF;
              }

              .home_card_content_img_container {
                margin-top     : 7px;
                display        : flex;
                justify-content: center;

                .sidemenu_header_effect_img {
                  width : 151px;
                  height: 47px;
                }
              }
            }

            .second_content {
              width         : 50%;
              font-size     : 14px;
              font-style    : normal;
              font-weight   : 400;
              line-height   : 16px;
              letter-spacing: 0em;
              text-align    : left;
              color         : var(--white);

              .second_content_btn_container {
                margin-top: 24px;

                .second_content_btn {
                  background   : #F5C242;
                  border-radius: 25px;
                  font-style   : normal;
                  font-weight  : 600;
                  font-size    : 24px;
                  line-height  : 36px;
                  width        : 100%;
                  height       : 70px;
                  padding      : 17px 0;
                  text-align   : center;
                  border       : none;
                  color        : #FFF;
                }

                .second_content_btn:hover {
                  cursor: pointer;
                }
              }
            }
          }
        }
      }


    }

  }
}