:root {
    --grey : #535353;
    --green: #1EFD92;
    --red  : #E53222;
    --white: #FFFFFF;
}

.container {
    max-width: 1290px;
    padding  : 0 20px;
    margin   : 0 auto;
}

* {
    // color     : white;
    margin    : 0;
    box-sizing: border-box;
}

a:hover {
    cursor: pointer;
}

a:link {
    text-decoration: none;
}

body {
    // background-color: var(--grey);
    background-color: transparent;
}

@media (max-width: 768px) {
    .container {
        padding: 0 16px;
    }
}