.accordion {
    width      : 100%;
    position   : relative;
    margin-top : 11px;
    user-select: none;

    .main {
        width           : 100%;
        height          : 48px;
        border          : 1px solid #E53222;
        border-radius   : 10px;
        padding         : 0;
        background-color: #FFF;
        display         : flex;
        align-items     : center;
        position        : relative;


        .main_icon {
            position : absolute;
            width    : 20px;
            height   : 20px;
            top      : 50%;
            left     : 42px;
            transform: translate(0, -50%);

            * {
                color: var(--grey);
            }
        }

        .main_title {
            width         : 100%;
            height        : 22px;
            margin-left   : 206px;
            margin-right  : 42px;
            font-family   : Poppins;
            font-size     : 14px;
            font-style    : normal;
            font-weight   : 400;
            line-height   : 21px;
            letter-spacing: 0.02em;
            text-align    : left;
            color         : var(--grey);
        }

        .main_info {
            position        : absolute;
            top             : 50%;
            right           : 42px;
            transform       : translate(0, -50%);
            font-family     : Poppins;
            font-size       : 14px;
            font-style      : normal;
            font-weight     : 400;
            line-height     : 21px;
            letter-spacing  : 0.02em;
            text-align      : left;
            color           : var(--grey);
            z-index         : 1;
            background-color: #FFF;
        }

        &:hover {
            cursor: pointer;
        }
    }

    .content {
        width           : 100%;
        margin-top      : 8px;
        border          : 1px solid #B4B4B4;
        border-radius   : 10px;
        background-color: #FFF;
    }
}