.group {
    width        : 100%;
    height       : 55px;
    background   : #FFFFFF;
    border       : none;
    border-radius: 18px;
    padding      : 4px 8px;
    margin-bottom: 16px;

    .group_container {
        display    : flex;
        align-items: center;

        .group_avatar {
            width           : 47px;
            height          : 47px;
            border-radius   : 50%;
            border          : none;
            background-color: #FFF;

            .avatar {
                width        : 100%;
                height       : 100%;
                border       : none;
                border-radius: 50%;
            }
        }

        .group_info_container {
            margin-left: 8px;

            .group_name {
                font-family   : Poppins;
                font-size     : 14px;
                font-style    : normal;
                font-weight   : 500;
                line-height   : 21px;
                letter-spacing: 0em;
                text-align    : left;
                color         : #B02418;
            }

            .group_members {
                margin-top    : 5px;
                font-family   : Poppins;
                font-size     : 8px;
                font-style    : normal;
                font-weight   : 500;
                line-height   : 12px;
                letter-spacing: 0em;
                text-align    : left;
                color         : #C4C4C4;
            }
        }
    }
}

.group {
    cursor: pointer;
}