.main {
  overflow            : hidden;
  position            : relative;
  width               : 100%;
  height              : 100%;
  // background-image : url('../../assets/image/back/home_back.png');
  // background-repeat: no-repeat;
  // background-size  : 100% 100%;
  pointer-events      : all;

  .edit_profile {
    width           : 595px;
    height          : 76%;
    position        : absolute;
    left            : 50%;
    top             : 23%;
    transform       : translate(-50%, 0);
    background-color: transparent;
    overflow        : auto;

    .edit_card {
      width           : 100%;
      height          : 500px;
      position        : relative;
      background-color: #FFF;
      border          : none;
      border-radius   : 30px;
      padding         : 60px 35px 17px 35px;

      .name {
        width: 100%;

        .title {
          font-family   : Poppins;
          font-size     : 18px;
          font-style    : normal;
          font-weight   : 600;
          line-height   : 48px;
          letter-spacing: 0em;
          text-align    : left;
          color         : #B02418;
        }

        .input {
          width: 100%;

          &>input {
            width           : 100%;
            height          : 36px;
            border          : 1px solid #B4B4B4;
            border-radius   : 10px;
            background-color: #FFF;
            padding-left    : 8px;
            font-family     : Poppins;
            font-size       : 18px;
            font-style      : normal;
            font-weight     : 400;
            line-height     : 48px;
            letter-spacing  : 0em;
            text-align      : left;
            color           : var(--grey);
            outline         : none;

            &:focus {
              color: #B4B4B4;
            }
          }
        }
      }

      .id {
        width   : 100%;
        position: relative;

        .title {
          font-family   : Poppins;
          font-size     : 18px;
          font-style    : normal;
          font-weight   : 600;
          line-height   : 48px;
          letter-spacing: 0em;
          text-align    : left;
          color         : #B02418;
        }

        .input {
          width        : 100%;
          height       : 36px;
          border-radius: 10px;
          border       : 1px solid #EBEBEB;
          display      : flex;
          align-items  : center;
          padding-left : 8px;
          padding-right: 12px;

          &>input {
            width           : 100%;
            height          : 100%;
            outline         : none;
            border          : none;
            background-color: transparent;
            color           : #EBEBEB;
          }

          .input_icon {
            width : 24px;
            height: 24px;
            color : #EBEBEB;
          }
        }

        .lock {
          width          : 100%;
          height         : 25px;
          display        : flex;
          align-items    : center;
          justify-content: flex-end;
          font-family    : Poppins;
          font-size      : 10px;
          font-style     : normal;
          font-weight    : 400;
          line-height    : 25px;
          letter-spacing : 0em;
          color          : #A9A9A9;
        }
      }

      .bio {
        width     : 100%;
        margin-top: 17px;

        .title {
          font-family   : Poppins;
          font-size     : 18px;
          font-style    : normal;
          font-weight   : 600;
          line-height   : 48px;
          letter-spacing: 0em;
          text-align    : left;
          color         : #B02418;
        }

        .input_text {
          width   : 100%;
          height  : 107px;
          position: relative;

          &>textarea {
            width         : 100%;
            height        : 100%;
            border        : 1px solid #B4B4B4;
            border-radius : 10px;
            padding       : 8px;
            font-family   : Poppins;
            font-size     : 14px;
            font-style    : normal;
            font-weight   : 300;
            line-height   : 16px;
            letter-spacing: 0em;
            color         : var(--grey);
            outline       : none;
            resize        : none;

            &:focus {
              color: #B4B4B4;
            }
          }

          .text_size {
            position      : absolute;
            bottom        : 10px;
            right         : 12px;
            font-family   : Poppins;
            font-size     : 14px;
            font-style    : normal;
            font-weight   : 300;
            line-height   : 16px;
            letter-spacing: 0em;
            color         : #EBEBEB;
          }
        }
      }

      .save {
        position : absolute;
        left     : 50%;
        bottom   : 22px;
        transform: translateX(-124px);


        .save_container {
          position: relative;
          display : flex;

          .prev_btn {
            width           : 19px;
            height          : 31px;
            border-radius   : 18px;
            border          : 1px solid #F5C242;
            background-color: #FFF;
            color           : #F5C242;
            display         : flex;
            justify-content : center;
            align-items     : center;
            margin-right    : 16px;

            &:hover {
              cursor: pointer;
            }
          }

          .save_btn {
            width           : 178px;
            height          : 31px;
            border          : none;
            border-radius   : 12px;
            background-color: #F5C242;
            display         : flex;
            justify-content : center;
            align-items     : center;
            font-family     : Poppins;
            font-size       : 14px;
            font-style      : normal;
            font-weight     : 600;
            line-height     : 21px;
            letter-spacing  : 0em;
            color           : #FFF;

            &:hover {
              cursor: pointer;
            }
          }
        }
      }

      .alert {
        width           : 234px;
        height          : 42px;
        border-radius   : 30px;
        box-shadow      : 0px 4px 4px 0px #00000040;
        display         : flex;
        justify-content : center;
        align-items     : center;
        font-family     : Poppins;
        font-size       : 11px;
        font-style      : normal;
        font-weight     : 500;
        line-height     : 48px;
        letter-spacing  : 0em;
        text-align      : center;
        background-color: #FFF;
        color           : #039A00;
        position        : absolute;
        left            : 50%;
        bottom          : 67px;
        transform       : translate(-50%, 0);

        &:hover {
          cursor: pointer;
        }
      }

    }

    &::-webkit-scrollbar {
      width: 8px;
    }

    &::-webkit-scrollbar-track {
      background-color: unset;
      border-radius   : 100px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: transparent;
      border          : 3px solid rgba(0, 0, 0, 0);
      background-clip : padding-box;
      border-radius   : 6px;
    }

    &:hover::-webkit-scrollbar-thumb {
      background-color: rgba(177, 34, 34, 1);
    }

  }

  .home_btn {
    width           : 69px;
    height          : 66px;
    border          : none;
    border-radius   : 15px;
    background-color: #F5C242;
    display         : flex;
    align-items     : center;
    justify-content : center;
    position        : absolute;
    top             : 47px;
    left            : 34px;

    &>img {
      width : 46px;
      height: 46px;
    }
  }

  // .avatar {
  //   width           : 141px;
  //   height          : 141px;
  //   border          : none;
  //   border-radius   : 50%;
  //   box-shadow      : 0px 4px 17px 0px #00000040;
  //   background-color: #F5C242;
  //   position        : absolute;
  //   top             : 23%;
  //   left            : 50%;
  //   transform       : translate(-50%, -50%);

  //   &>img {
  //     width        : 100%;
  //     height       : 100%;
  //     border-radius: 50%;
  //   }
  // }

  .avatar_container {
    width           : 141px;
    height          : 141px;
    border-radius   : 50%;
    border          : none;
    background-color: #F5C242;
    position        : absolute;
    top             : 23%;
    left            : 50%;
    transform       : translate(-50%, -50%);
    box-shadow      : 0px 4px 17px 0px #00000040;
    z-index         : 10;

    .rel_container {
      width   : 100%;
      height  : 100%;
      position: relative;

      &>img {
        width        : 100%;
        height       : 100%;
        border-radius: 50%;
        border       : none;
      }

      .edit_btn {
        position        : absolute;
        right           : 10px;
        bottom          : 2px;
        width           : 26px;
        height          : 26px;
        background-color: #FFF;
        color           : #F5C242;
        border          : none;
        border-radius   : 50%;
        box-shadow      : 0px 4px 4px 0px #00000040;

        &:hover {
          cursor: pointer;
        }
      }
    }
  }
}

@media (max-width: 1280px) {}