.winoption {
    position: absolute;
    right   : 0;
    top     : 0;
    width   : 15px;
    height  : 10px;

    .winoption_container {
        width          : 100%;
        height         : 100%;
        display        : flex;
        justify-content: space-between;
        align-items    : center;

        .option_btn {
            width: 12px;
            color: white;

            .option_img {
                width: 100%;
            }
        }

        .option_btn:hover {
            cursor: pointer;
        }
    }
}